import React from 'react';

import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import WxMainObsCardCurrent from "./wxmainobscardcurrent";
import { WxMainObsCardRecords } from './wxmainobscardrecords';
import WxMainObsCardGraph from './wxmainobscardgraph.';

export class WxMainObsCard extends React.Component {

    render() {

        let header = `${this.props.weather.label}`;
        if (this.props.weather.unit.replace(' ', '') !== '') {
            header += ` (${this.props.weather.unit.replace(' ', '')})`;
        }

        //{this.props.weather.label} ({this.props.weather.unit.replace(' ', '')})

        return (
            <Card>
                <Card.Header className="bg-dark bg-gradient text-center text-white">
                    {header}

                </Card.Header>
                <Card.Body className="bg-dark bg-opacity-75">
                    <Row>
                        <Col xs={5}>
                            <WxMainObsCardCurrent key={'obs-curr' + this.props.obs} obs={this.props.obs} current={this.props.weather.current} CONSTS={this.props.CONSTS}/>
                        </Col>
                        <Col xs={7}>
                            <WxMainObsCardRecords key={'obs-rec' + this.props.obs} obs={this.props.obs} weather={this.props.weather} CONSTS={this.props.CONSTS}/>
                        </Col>
                    </Row>
                    <Row className="text-center text-white">
                        <Col xs={12}>
                            <WxMainObsCardGraph key={'obs-graph' + this.props.obs} obs={this.props.obs} weather={this.props.weather}/>
                        </Col>
                    </Row>
                </Card.Body>
            </Card>
        )
    }
}
export default WxMainObsCard;