import React from "react";
import ListGroup from "react-bootstrap/ListGroup";

class WxAlertSeparator extends React.Component {
    render() {
        return (
            <ListGroup.Item as="li" variant="dark" className={this.props.className + " pt-0 pb-0 ps-1 pe-1 m-0 bg-dark bg-opacity-30 text-white"}>
                {this.props.text}
            </ListGroup.Item>
        )
    }
}
export default WxAlertSeparator;