import React from "react";

import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import WxMainObsCard from "./observationpanecomponents/wxmainobscard";

class WxObservationPane extends React.Component {
    static FIELDS = ['otemp', 'oheat', 'ochill', 'odew', 'orh', 'bar', 'wspeed', 'wgust', 'rain', 'sr', 'uv', 'itemp', 'irh'];

    render() {
        this.mainWeather = {};

        if (this.props.weather !== null && this.props.weather.observations !== undefined) {
            this.mainWeather = this._convertWeather();
        }

        return(
            <Row className="row-cols-1 row-cols-sm-2 row-cols-lg-3 row-cols-xl-4">
                { this.props.weather !== undefined &&
                    Object.keys(this.mainWeather).map((obs, i) => (
                        <Col className="pb-2 ps-1 pe-1" key={'mainCol' + obs} weather={this.mainWeather[obs]}>
                            <WxMainObsCard key={'mainCard' + obs} obs={obs} weather={this.mainWeather[obs]} CONSTS={this.props.CONSTS}/>
                        </Col>
                    ))
                                    
                }
            </Row>
        )
    }

    _convertWeather() {
        let wx = {};
        let seriesSpan = 'h';
        const currentTime = new Date()
        const timeLimits = {
            "h1": currentTime.getTime() - (60*60*1000),
            "h24": currentTime.getTime() - (24*60*60*1000),
            "d1": new Date(currentTime.getFullYear(), currentTime.getMonth(), currentTime.getDate(), 0,0,0).getTime(),
            "d7": currentTime.getTime() - (7*24*60*60*1000),
            "d30": currentTime.getTime() - (30*24*60*60*1000),
            "m1": new Date(currentTime.getFullYear(), currentTime.getMonth(), 0,0,0,0).getTime(),
            "d365": currentTime.getTime() - (365*24*60*60*1000),
            "y1": new Date(currentTime.getFullYear(), 0,0,0,0,0).getTime(),
            "at": currentTime.getTime() - (365*24*60*60*1000)
        };

        if (this.props.page.option === 'h1' || this.props.page.option === 'h24' || this.props.page.option === 'd1') {
            seriesSpan = 'h';
        } else if (this.props.page.option === 'd7' || this.props.page.option === 'd30' || this.props.page.option === 'm1') {
            seriesSpan = 'd';
        } else if (this.props.page.option === 'd365' || this.props.page.option === 'y1' || this.props.page.option === 'at') {
            seriesSpan = 'y';
        }

        for(const obs in WxObservationPane.FIELDS) {
            if (this.props.weather.observations[WxObservationPane.FIELDS[obs]].current !== null) {

                let limitedSeries = this._parseSeries(this.props.weather.observations[WxObservationPane.FIELDS[obs]].series[seriesSpan], timeLimits[this.props.page.option]);

                wx[WxObservationPane.FIELDS[obs]] = {
                    current: this.props.weather.observations[WxObservationPane.FIELDS[obs]].current,
                    unit: this.props.weather.observations[WxObservationPane.FIELDS[obs]].units,
                    label: this.props.weather.observations[WxObservationPane.FIELDS[obs]].label,
                    records: this.props.weather.observations[WxObservationPane.FIELDS[obs]].summary[this.props.page.option],
//                    series: this.props.weather.observations[WxObservationPane.FIELDS[obs]].series[seriesSpan]
                    series: limitedSeries
                }

                if (this.props.weather.observations[WxObservationPane.FIELDS[obs]].additional !== null) {
                    wx[WxObservationPane.FIELDS[obs]].additional = this.props.weather.observations[WxObservationPane.FIELDS[obs]].additional;
                }

                // Additional is the rain total for period
                if (WxObservationPane.FIELDS[obs] === 'rain') wx[WxObservationPane.FIELDS[obs]].additional = {"total" : this.props.weather.observations[WxObservationPane.FIELDS[obs]].summary[this.props.page.option].total};
                // Additional is the ET total for the period
                if (WxObservationPane.FIELDS[obs] === 'sr') wx[WxObservationPane.FIELDS[obs]].additional = {"ET total": this.props.weather.observations.et.summary[this.props.page.option].total};
            }
        }

        return wx;
    }

    _parseSeries(series, limits) {
        let output = []
        for (let i = 0; i < series.length; i++) {
            if (series[i][0] >= (limits/1000)) {
                if (series[i].length === 2) {
                    output.push([new Date(series[i][0]*1000), series[i][1]]);
                } else {
                    output.push([new Date(series[i][0]*1000), series[i][1], series[i][2]]);
                }
            }
        }
        return output;
    }
}
export default WxObservationPane;