import React from 'react';

import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

class WxNavigation extends React.Component {

    constructor(props) {
        super(props);

        this.state = ({
            wxDisabled: false,
            camDisabled: false,
            durationDisabled: false
        })

        this.rangeLabel = React.createRef();

        this.handleChangeProps = this.handleChangeProps.bind(this);
    }

    componentDidMount() {
        //console.log(this.rangeLabel.innerHTML);
    }

    handleChangeProps(eventKey) {

        // Handle enable/disable button state
        if (Object.keys(this.props.CONFIG.stations).includes(eventKey)) {
            if (this.props.CONFIG.stations[eventKey].status === this.props.CONSTS.stationTypes.full) {
                this.setState({wxDisabled: false, camDisabled: false, durationDisabled: false});
            } else if (this.props.CONFIG.stations[eventKey].status === this.props.CONSTS.stationTypes.wxOnly) {
                this.setState({wxDisabled: false, camDisabled: true, durationDisabled: false});
            } else if (this.props.CONFIG.stations[eventKey].status === this.props.CONSTS.stationTypes.camOnly) {
                this.setState({wxDisabled: true, camDisabled: false, durationDisabled: true});
            } else if (this.props.CONFIG.stations[eventKey].status === this.props.CONSTS.stationTypes.friend) {
                this.setState({wxDisabled: false, camDisabled: true, durationDisabled: true});
            } else {
                this.setState({wxDisabled: true, camDisabled: true, durationDisabled: true});
            }
        }
        this.props.handleChangeProps(eventKey);        
    }

    render() {

        return (
            <Nav id="WxNav" className="bg-dark bg-gradient pt-1 pb-1" variant="pills" onSelect={this.handleChangeProps}>
                <NavDropdown title="Station" id="nav-station-dropdown">
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="rockford" href="#">Rockford</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="byron" href="#">Byron Center</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="fowlerville" href="#">Fowlerville</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="gainestwp" href="#">Gaines Twp</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="jamestown" href="#">Jamestown</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="hamilton" href="#">Hamilton</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="hopkins" href="#">Hopkins</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="lowell" href="#">Lowell</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="martin" href="#">Martin</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="saranac" href="#">Saranac</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="sunfield" href="#">Sunfield</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey="zeeland" href="#">Zeeland</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown title="Friends" id="nav-friend-dropdown">
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="goodrich" href="#">Goodrich</Nav.Link>
                        </NavDropdown.Item>
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="grandblanc" href="#">Grand Blanc</Nav.Link>
                        </NavDropdown.Item>
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="greenville" href="#">Greenville</Nav.Link>
                        </NavDropdown.Item>
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="eastport" href="#">Easport</Nav.Link>
                        </NavDropdown.Item>
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="hartland" href="#">Hartland</Nav.Link>
                        </NavDropdown.Item>
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="kalamazoo" href="#">Kalamazoo</Nav.Link>
                        </NavDropdown.Item>
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="niles" href="#">Niles</Nav.Link>
                        </NavDropdown.Item>
                        <NavDropdown.Item>
                            <Nav.Link className="p-0 m-0" eventKey="oxford" href="#">Oxford</Nav.Link>
                        </NavDropdown.Item>
                    </NavDropdown>
                </NavDropdown>
                <NavDropdown title="Page" id="nav-page-dropdown">
                    <NavDropdown.Item disabled={this.state.wxDisabled}>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.siteObs} href="#">Station Weather</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item disabled={this.state.camDisabled}>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.siteCam} href="#">Station Camera</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item disabled={this.state.camDisabled}>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.siteImg} href="#">Station Image Archive</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item disabled={this.state.wxDisabled}>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.siteNoaa} href="#">Station NOAA</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.sysAlert} href="#">Alert Log</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.sysSumm} href="#">System Summary</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.sysMap} href="#">System Map</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.page.sysImgs} href="#">System Images</Nav.Link>
                    </NavDropdown.Item>
                </NavDropdown>
                <NavDropdown className="d-none" disabled={this.state.durationDisabled} title="Duration" id="nav-option-dropdown">
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.h1} href="#">1-Hour</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.h24} href="#">24-Hours</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.d1} href="#">1-Day</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.d7} href="#">7-Days</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.d30} href="#">30-Days</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.m1} href="#">1-Month</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.d365} href="#">365-Days</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.y1} href="#">1-Year</Nav.Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                        <Nav.Link className="p-0 m-0" eventKey={this.props.CONSTS.viewOpts.options.at} href="#">All-Time</Nav.Link>
                    </NavDropdown.Item>
                </NavDropdown>
                <Form style={{width: '420px'}} className="ms-1 row row-cols-xs-auto align-items-left">
                    <Row>
                        <Col className="col-3">
                            <Form.Label className="b-1 m-1 p-1 text-white">Duration: </Form.Label>
                        </Col>
                        <Col className="col-6">
                            <Form.Range className="b-1 m-1 p-1" disabled={this.state.durationDisabled} value={this._durationToSlider(this.props.page.option)} min="0" max={Object.keys(this.props.CONSTS.viewOpts.options).length - 1} step="1" onChange={(e) => {this.handleRangeChange(e)}}/>
                        </Col>
                        <Col>
                            <Form.Label ref={this.rangeLabel} className="col-3 b-1 m-1 p-1 text-white">{this.props.page.option}</Form.Label>
                        </Col>
                    </Row>
                </Form>
                <Nav.Item className="ms-auto me-0 border rounded border-white">
                    <Nav.Link eventKey="homeNavBtn" href="#"><i className="fa fa-home"/></Nav.Link>
                </Nav.Item>
                <Nav.Item className="ms-0 me-0 border rounded border-white">
                    <Nav.Link eventKey="refreshNavBtn" href="#"><i className="fa fa-refresh"/></Nav.Link>
                </Nav.Item>
            </Nav>
        )
    }

    _sliderToDuration(value) {
        return this.props.CONSTS.viewOpts.options[Object.keys(this.props.CONSTS.viewOpts.options)[value]]
    }

    _durationToSlider(value) {
        //console.log(value);
        return Object.keys(this.props.CONSTS.viewOpts.options).indexOf(value);
    }

    handleRangeChange(e) {
        this.rangeLabel.current.innerHTML = this._sliderToDuration(e.target.value);
        this.handleChangeProps(this._sliderToDuration(e.target.value));
    }
}
export default WxNavigation;