import React from 'react';

import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import WxImageModal from '../wxmodals/wximagemodal';

export class WxSystemCameraPane extends React.Component {

    render() {

        let stations = [];

        for(const i in this.props.CONFIG.stations) {
            if (this.props.CONFIG.stations[i].status === 'activeFull' || this.props.CONFIG.stations[i].status === 'activeCam') {
                stations.push(this.props.CONFIG.stations[i]);
            }
        }

        let cardList = Object.keys(stations).map((sta, i) => (
            <Col className="pb-2 ps-1 pe-1" key={'staCam' + sta}>
                <Card key={'staImg'+sta}>
                    <Card.Img variant="top" src={stations[sta].image.camera + '?' + new Date().getTime()}/>
                    <WxImageModal buttonTxt="Enlarge" src={stations[sta].image.camera + '?' + new Date().getTime()} title={stations[sta].name}/>
                    <Card.Body>
                        <Card.Text className="text-center text-capitalize"><h5>{stations[sta].name}</h5></Card.Text>
                    </Card.Body>
                </Card>
            </Col>
        ))

        return (
            <Row className="row-cols-1 row-cols-sm-2 row-cols-lg-3 row-cols-xl-4">
                {cardList}
            </Row>
        )
    }
}
export default WxSystemCameraPane;