import React from 'react';

export class WxDailyForecastRow extends React.Component {

    render() {
        let day = 'bg-light';

        if (!this.props.forecast.isDaytime) day = 'bg-dark bg-opacity-25 text-dark';

        return (
            <tr className={day}>
                <td>{this.props.forecast.name}</td>
                <td><img src={this.props.forecast.icon} alt={this.props.forecast.shortForecast}/></td>
                <td>{this.props.forecast.detailedForecast}</td>
            </tr>
        )
    }
}
export default WxDailyForecastRow;