// React and PropType Imports
import React from 'react';
import PropTypes from 'prop-types';

// React-Leaflet Imports
import { MapContainer, TileLayer, Marker, Polygon, Popup } from 'react-leaflet';
import * as L from 'leaflet';

import WxMapLegend from './wxmapcomponents/wxmaplegend';
import WxRadarEngine from './wxmapcomponents/wxradarengine';

/**
 * Class to display the leaflet map in a component
 * 
 * Displays a leaflet map
 * 
 *      Extends: React.Component
 *      Main Component: React-Leaflet.MapContainer
 * 
 *      Properties:
 *          id - DOM Id
 *          station - station id from wxconfig.json
 *          radarVisible - Boolean is radar visible
 *          stationVisible - Boolean is station visible
 *          warnVisible - Boolean are polygons visible
 *          polygons - Array of polygons to display on the map
 *          variant - Variant of polygons that are displayed
 *          CONFIG - wxconfig.json file contents
 */
export class WxMapComponent extends React.Component {

    radarCentered = false;

    constructor(props) {
        super(props);

        setInterval(() => {this.map.invalidateSize(); this.radarCentered = true;}, 500)
    }

    componentDidMount() {

    } 

    /**
     * Renders component to the screen
     * @returns React Component
     */
    render() {
        const LeafIcon = L.Icon.extend({
            options: {}    
        });

        // Object used to set colors of polygons based on variant passed
        const variantColors = {
            success: 'green',
            info: 'blue',
            warning: 'yellow',
            danger: 'red',
            extreme: 'purple'
        }

        // Status color icons
        this.statusColors = {
            error: new LeafIcon({
                iconUrl: "https://maps.google.com/mapfiles/kml/paddle/red-blank.png",
                iconSize: [30,30],
                iconAnchor: [15,30]
            }),
            inactive: new LeafIcon({
                iconUrl: "https://maps.google.com/mapfiles/kml/paddle/wht-blank.png",
                iconSize: [30,30],
                iconAnchor: [15,30]
            }),
            friend: new LeafIcon({
                iconUrl: "https://maps.google.com/mapfiles/kml/paddle/ylw-blank.png",
                iconSize: [30,30],
                iconAnchor: [15,30]
            }),
            activeFull: new LeafIcon({
                iconUrl: "https://maps.google.com/mapfiles/kml/paddle/grn-blank.png",
                iconSize: [30,30],
                iconAnchor: [15,30]
            }),
            activeWx: new LeafIcon({
                iconUrl: "https://maps.google.com/mapfiles/kml/paddle/blu-blank.png",
                iconSize: [30,30],
                iconAnchor: [15,30]
            }),
            activeCam: new LeafIcon({
                iconUrl: "https://maps.google.com/mapfiles/kml/paddle/pink-blank.png",
                iconSize: [30,30],
                iconAnchor: [15,30]
            })
        }

        // Get the station's location for the map
        this.stationLocation = [this.props.CONFIG.stations[this.props.station].lat, this.props.CONFIG.stations[this.props.station].lon];
        let stationList = [];

        stationList.push(<Marker key="start" position={this.stationLocation} icon={this.statusColors[this.props.CONFIG.stations[this.props.station].status]}/>);

        if (this.props.showAllStations) {
            stationList = this._getAllStations(this.props.CONFIG.stations);
        }

        // Return the component
        return (
            <MapContainer ref={(ref) => { this.map = ref;}} id={this.props.id} center={this.stationLocation} zoom={6} zoomControl={!this.props.wag} fullscreenControl={!this.props.wag} scrollWheelZoom={false} maxZoom={11} className="markercluster-map" animate={false}>
                <TileLayer
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                {this.props.radarVisible &&
                    <WxRadarEngine animate={this.props.radarAnimate}/>
                }
                {(this.props.stationVisible || this.props.showAllStations) && 
                    <>
                        {stationList}
                    </>
                }
                {(this.props.warnVisible && this.props.polygons !== null) &&
                    <Polygon
                        positions={this.props.polygons}
                        pathOptions={{color: variantColors[this.props.variant]}}
                    />
                }
                {(this.props.legendVisible) && <WxMapLegend map={this.map}/>}
            </MapContainer>
        )
    }



    _getAllStations(stations) {
        let data = [];

        for (const s in stations) {
            data.push(React.createElement(Marker, {key: s, position: [stations[s].lat, stations[s].lon], icon: this.statusColors[this.props.CONFIG.stations[s].status]}, React.createElement(Popup, {key: 'pop'+s}, this.props.CONFIG.stations[s].name)));
        }

        return data;
    }
}
export default WxMapComponent;

WxMapComponent.propTypes = {
    id: PropTypes.string,
    station: PropTypes.string.isRequired,
    radarVisible: PropTypes.bool,
    stationVisible: PropTypes.bool,
    warnVisible: PropTypes.bool,
    polygons: PropTypes.array,
    variant: PropTypes.string,
    CONFIG: PropTypes.object.isRequired
}

WxMapComponent.defaultProps = {
    id: 'map-' + new Date().getTime(),
    radarVisible: false,
    stationVisible: false,
    warnVisible: false,
    polygons: [],
    variant: 'success',
}