import React from 'react';

import Popover from 'react-bootstrap/Popover';
import ProgressBar from 'react-bootstrap/ProgressBar';
import * as ColorUtils from '../../util/wxcolorutils';

class WxAlertTimelinePopover extends React.Component {

    render() {

        let barStart = new Date().getTime();
        let barEnd = new Date().getTime() + 172800000;

        let onsetPosition = ColorUtils.findScalePercentPosition(new Date(this.props.alert.onset).getTime(), {min: barStart, max: barEnd}) * 100;
        let endPosition = ColorUtils.findScalePercentPosition(new Date(this.props.alert.expires).getTime(), {min: barStart, max: barEnd}) * 100;
        if (this.props.alert.ends !== null) endPosition = ColorUtils.findScalePercentPosition(new Date(this.props.alert.ends).getTime(), {min: barStart, max: barEnd}) * 100;

        return (
            <Popover.Body>
                <ProgressBar>
                    <ProgressBar variant="dark" now={onsetPosition} key="before"/>
                    <ProgressBar variant={this.props.alert.severeColor} now={endPosition-onsetPosition} key="alert"/>
                    <ProgressBar variant="dark" now={100 - (onsetPosition + endPosition)} key="after"/>
                </ProgressBar>
                Starts: {new Date(this.props.alert.onset).toLocaleString()}<br/>
                Ends: {new Date(this.props.alert.ends).toLocaleString()}<br/>
            </Popover.Body>
        )
    }


}
export default WxAlertTimelinePopover;