import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import WxMapComponent from '../wxmapcomponent';

export function WxRadarModal(data) {
    const [show, setShow] = useState(false);

    return (
        <>
            <Button size="sm" onClick={() => setShow(true)} className={"bg-light bg-opacity-75 text-black border-dark"}>
                Radar
            </Button>
            <Modal
                show={show}
                onHide={() => setShow(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="aqiModal">
                        Station Radar
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body style={{height: "300px"}}>
                    <WxMapComponent id="radarMap" station={data.station} radarVisible={true} stationVisible={true} CONFIG={data.CONFIG} radarAnimate={true}/>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
export default WxRadarModal;