import React from 'react';

import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import WxSummaryCardRecords from './wxsummarycardrecords';
import WxSummaryCardGraph from './wxsummarycardgraph';

export class WxSummaryObsCard extends React.Component {

    render() {
        let wx = this._formatWeather(this.props.weather, this.props.field);
        let title= this._formatTitle(this.props.weather, this.props.field, this.props.CONFIG.pageDefault.station);
        
        return (
            <Card>
                <Card.Header className="bg-dark bg-gradient text-center text-white">
                    {title}
                </Card.Header>
                <Card.Body className="bg-dark bg-opacity-75">
                    <Row>
                        <Col>
                            <WxSummaryCardRecords field={this.props.field} weather={wx} CONSTS={this.props.CONSTS}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <WxSummaryCardGraph field={this.props.field} weather={this.props.weather}/>
                        </Col>
                    </Row>
                </Card.Body>
            </Card>
        )
    }

    _formatTitle(weather, field, station) {
        return weather[station][field].title;
    }

    _formatWeather(weather, field) {
        let record = {};

        for (const station in weather) {

            let rec = {
                station: station,
                title: weather[station][field].title,
                min: weather[station][field].min,
                avg: weather[station][field].avg,
                max: weather[station][field].max,
                current: weather[station][field].current
            };

            if (isFinite(rec.min)) rec.min = new Number(rec.min).toFixed(2);
            if (isFinite(rec.avg)) rec.avg = new Number(rec.avg).toFixed(2);
            if (isFinite(rec.max)) rec.max = new Number(rec.max).toFixed(2);
            if (isFinite(rec.current)) rec.current = new Number(rec.current).toFixed(2);

            record[station] = rec;
        }

        return record;
    }
}
export default WxSummaryObsCard;