import React from "react";

import Alert from "react-bootstrap/Alert";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Stack from "react-bootstrap/Stack";

import WxWagObservation from "./wagcomponents/wxwagobservation";
import WxMapComponent from "./wxmapcomponent";

class WxWagCard extends React.Component {
    static FIELDS = ['otemp', 'oheat', 'ochill', 'orh', 'wspeed', 'wgust', 'rain', 'sr', 'uv'];

    render() {
        this.wagWeather = {};
        let showDistance = null;

        if(this.props.weather !== null && this.props.weather.observations !== undefined) {
            this.wagWeather = this._convertWeather();
        }

        if (this.props.station.distance > 0) showDistance = (<Alert variant="light" className="p-1 m1 text-center text-capitalize">At {this.props.station.station} Station ({this.props.station.distance}km away)</Alert>);

        return (
            <Card id="WxWagCard" style={{height: '100%'}}>
                <Card.Header className="bg-dark text-white bg-gradient"><h5>Weather At-A-Glance</h5></Card.Header>
                <Card.Body className="bg-dark text-white bg-opacity-75">
                    <Container style={{height: '75%'}}>
                        <Row>
                            <Col>
                                {showDistance}
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                {this.wagMessage}
                            </Col>
                        </Row>
                        <Row style={{height: '100%'}}>
                            <Col xs={8} style={{height: '100%'}}>
                                <Stack gap={0}>
                                    { this.props.weather !== undefined &&
                                        Object.keys(this.wagWeather).map((obs, i) => (
                                            <WxWagObservation key={'WAG' + obs} obs={obs} weather={this.wagWeather[obs]} CONSTS={this.props.CONSTS}/>
                                        ))
                                    }
                                </Stack>
                            </Col>
                            <Col xs={4}>
                                <WxMapComponent id="wagMap" wag={true} station={this.props.page.station} radarVisible={true} stationVisible={true} CONFIG={this.props.CONFIG} radarAnimate={true}/>
                            </Col>
                        </Row>
                    </Container>
                </Card.Body>
            </Card>
        )
    }

    _convertWeather() {
        let wx = {};

        for(const obs in WxWagCard.FIELDS) {
            if (this.props.weather.observations[WxWagCard.FIELDS[obs]].current !== null) {
                wx[WxWagCard.FIELDS[obs]] = {
                    current: this.props.weather.observations[WxWagCard.FIELDS[obs]].current,
                    unit: this.props.weather.observations[WxWagCard.FIELDS[obs]].units,
                    label: this.props.weather.observations[WxWagCard.FIELDS[obs]].label
                }
                if (WxWagCard.FIELDS[obs] === 'wspeed') wx.wspeed.add = this.props.weather.observations[WxWagCard.FIELDS[obs]].additional.compass;
                if (WxWagCard.FIELDS[obs] === 'wgust') wx.wgust.add = this.props.weather.observations[WxWagCard.FIELDS[obs]].additional.compass;
                if (WxWagCard.FIELDS[obs] === 'rain') wx.rain.add = this.props.weather.observations[WxWagCard.FIELDS[obs]].summary.h24.total;
            }
        }

        // Remove observations with no intrinsic value
        if (wx.ochill !== undefined && wx.ochill.current >= wx.otemp.current) delete wx.ochill;
        if (wx.oheat !== undefined && wx.oheat.current <= wx.otemp.current) delete wx.oheat;
        if (wx.wgust !== undefined && wx.wgust.current === 0) delete wx.wgust;
        if (wx.rain !== undefined && (wx.rain.current <= 0 && wx.rain.add <= 0)) delete wx.rain;
        if (wx.sr !== undefined && wx.sr.current === 0) delete wx.sr;
        if (wx.uv !== undefined && wx.uv.current === 0) delete wx.uv;

        // Set message
        this.wagMessage = this._getWagMessage(wx);

        return wx;
    }

    _getWagMessage(wx) {

        let msg = null;
        let level = 'warning';
        
        if (wx.oheat !== undefined && wx.oheat.current > 95) {
            msg = 'Drink water and find some shade! High heat index.';
            level = 'danger';
        } else if (wx.ochill !== undefined && wx.ochill.current < -15) {
            msg = 'Button up your overcoat! Dangerous wind chill';
            level = 'danger';
        } else if (wx.otemp !== undefined && wx.otemp.current > 90) {
            msg = 'Go jump in a lake! It is hot.';
        } else if (wx.otemp !== undefined && wx.otemp.current < 10) {
            msg = 'Did you wear your longjohns? It is chilly.';
        } else if (wx.wind !== undefined && wx.wind.current > 25) {
            msg = 'Hold on to your hat! It is windy.';
        } else if (wx.wind !== undefined && wx.otemp !== undefined && wx.wind.current > 10 && wx.otemp.current > 59) {
            msg = 'It is good kite flying weather.';
        } else if (wx.wind !== undefined && wx.otemp !== undefined && wx.wind.current > 10 && wx.otemp.current < 40) {
            msg = 'An umbrella is of little use today.';
        } else if (wx.wind !== undefined && wx.wind.current > 10) {
            msg = 'The winds of change are blowing.';
        } else if (wx.rain !== undefined && wx.rain.current >= 6) {
            msg = 'It is raining lions and wolves.';
            level = 'danger';
        } else if (wx.rain !== undefined && wx.rain.current >= 3) {
            msg = 'It is raining cats and dogs.';
        } else if (wx.rain !== undefined && wx.rain.current > 0.05) {
            msg = 'Grab an umbrella it is raining.';
        } else if (wx.uv !== undefined && wx.uv.current >= 6) {
            msg = 'High UV index. Take precautions.';
        }

        if (msg === null) {
            return null;
        } else {
            return (<Alert variant={level} className="p-1 m1 text-center"><i className="text-start fa fa-solid fa-triangle-exclamation"></i> {msg} <i className="text-end fa fa-solid fa-triangle-exclamation"></i></Alert>);
        }
    }
}

export default WxWagCard;