// React and PropTypes Imports
import React from 'react';
import PropTypes from 'prop-types';

// React-Bootstrap Imports
import Alert from 'react-bootstrap/Alert';

/**
 * Class to display the AdminBanner Element
 * 
 * Displays the Administrative Message Banner on the
 *  main page.
 * 
 *      Extends: React.Component
 *      Main Component: React-Bootstrap Alert Component
 * 
 *      Properties:
 *          CONFIG - wxconfig.json file adminMessage contents
 */
export class WxAdminMessageBanner extends React.Component {
    /**
     * Constructor fo AdminBanner Element
     * @param {Object} props 
     */
    constructor(props) {
        super(props);

        // State variables
        //  show: Is alert shown?
        this.state = {show: false};
        // If the visible flag is set in the config, then show this banner
        if (this.props.CONFIG.visible) this.state.show = true; else this.state.show = false;
    }

    /**
     * Renders the component to the screen
     * @returns React Component
     */
    render() {
        // If show, then return the Alert element
        if (this.state.show) {
            return (
                <Alert variant={this.props.CONFIG.variant} className="mt-2 mb-2 p-1 ps-2" onClose={() => this.setState({show: false})} dismissible>
                    <Alert.Heading><i className="me-2 fa fa-triangle-exclamation"/>Administrative Message</Alert.Heading>
                    <p className="fs-7 m-1 p-0">{this.props.CONFIG.message}</p>
                </Alert>
            )
        }
    }
}
export default WxAdminMessageBanner;

// Prop Types Declaration
WxAdminMessageBanner.propTypes = {
    CONFIG: PropTypes.exact({
        message: PropTypes.string.isRequired,
        variant: PropTypes.string.isRequired,
        visible: PropTypes.bool.isRequired
    })
};

// Default Props
WxAdminMessageBanner.defaultProps = {
    CONFIG: {
        message: '',
        variant: 'success',
        visible: false
    }
}