import React from 'react';

import Table from 'react-bootstrap/Table';

import * as ColorUtils from '../../../util/wxcolorutils';

export class WxMainObsCardRecords extends React.Component {

    render() {

        let maxColorData = ColorUtils.getColorData(this.props.obs, this.props.weather.records.max[1], this.props.CONSTS.colorRanges);
        let maxObsStyle = {backgroundColor: maxColorData.bg.toString(), color: maxColorData.fg.toString()};
        let avgColorData = ColorUtils.getColorData(this.props.obs, this.props.weather.records.avg, this.props.CONSTS.colorRanges);
        let avgObsStyle = {backgroundColor: avgColorData.bg.toString(), color: avgColorData.fg.toString()};
        let minColorData = ColorUtils.getColorData(this.props.obs, this.props.weather.records.min[1], this.props.CONSTS.colorRanges);
        let minObsStyle = {backgroundColor: minColorData.bg.toString(), color: minColorData.fg.toString()};

        return(
            <Table size="sm" className="text-white">
                <tbody>
                <tr>
                    <td>Max</td>
                    <td style={maxObsStyle} title={new Date(this.props.weather.records.max[0]*1000).toLocaleString()}>{parseFloat(this.props.weather.records.max[1].toFixed(3)).toString()}</td>
                </tr>
                <tr>
                    <td>Avg</td>
                    <td style={avgObsStyle}>{parseFloat(this.props.weather.records.avg.toFixed(3)).toString()}</td>
                </tr>
                <tr>
                    <td>Min</td>
                    <td style={minObsStyle} title={new Date(this.props.weather.records.min[0]*1000).toLocaleString()}>{parseFloat(this.props.weather.records.min[1].toFixed(3)).toString()}</td>
                </tr>
                { this.props.weather.additional !== undefined && this.props.weather.additional !== null &&
                    Object.keys(this.props.weather.additional).map((add, i) => (
                        <tr key={i}>
                            <td key={i} className="text-capitalize">{add}</td>
                            <td>{this.props.weather.additional[add]}</td>
                        </tr>
                    ))                
                }
                </tbody>
            </Table>
        )
    }
}