import React from 'react';

import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';

import WxImageModal from '../wxmodals/wximagemodal.js';
import {getIndexedFilenames} from '../../util/wxcolorutils.js';

export class WxImageArchivePane extends React.Component {
    state={archive: null};

    constructor(props) {
        super(props);

        this.selectedArchive = null;
        this.state = {archives: null,
            archiveFiles: null};
    }

    componentDidMount() {
        this._getArchives(this.props.CONFIG.stations[this.props.page.station].image.archives);
    }

    _getArchives(archives) {
        this.setState({archives: Object.keys(archives)});
    }

    async _getArchiveImages(archive) {
        this.selectedArchive = archive;

        this.setState({archiveFiles: await getIndexedFilenames(this.props.CONFIG.stations[this.props.page.station].image.archives[archive], 'jpeg')})
    }

    render() {

        return(
            <>
                <Form>
                    <Form.Group>
                        <Form.Label className="text-end" column sm={2}>Archive</Form.Label>
                        <Form.Select onChange={(e) => this._getArchiveImages(e.target.value.toLowerCase())}>
                            { (this.state.archives !== null) && Object.keys(this.state.archives).map((archive, i) => (
                                <option key={i} value={this.state.archives[archive]}>{this.state.archives[archive]}</option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                </Form>
                <Row className="row-cols-1 row-cols-sm-2 row-cols-lg-3 row-cols-xl-4">
                    { (this.state.archiveFiles !== null) && 
                        Object.keys(this.state.archiveFiles).map((image, i) => (
                            <Col className="pb-2 ps-1 pe-1" key={i}>
                                <Card key={i}>
                                    <Card.Img variant="top" src={this.props.CONFIG.stations[this.props.page.station].image.archives[this.selectedArchive] + this.state.archiveFiles[image] + '?' + new Date().getTime()}/>
                                        <WxImageModal buttonTxt="Enlarge" src={this.props.CONFIG.stations[this.props.page.station].image.archives[this.selectedArchive] + this.state.archiveFiles[image] + '?' + new Date().getTime()} title={this.state.archiveFiles[image]}/>
                                    <Card.Body>
                                        <Card.Text className="text-center"><h5>{this.state.archiveFiles[image]}</h5></Card.Text>
                                    </Card.Body>
                                </Card>
                            </Col>
                        ))
                    }
                </Row>                
            </>
        )
    }
}
export default WxImageArchivePane;