import React from "react";

import Card from "react-bootstrap/Card";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import WxMainObsCardCurrent from "./observationpanecomponents/wxmainobscardcurrent";

class WxFriendObservationPane extends React.Component {
    constructor(props) {
        super(props);

        this.weather = null;
    }

    render() {

        // Weather Data
        const weatherData = async (station, api) => {
            const response = await fetch(`https://api.weather.com/v2/pws/observations/current?stationId=${station}&format=json&units=e&numericPrecision=decimal&apiKey=${api}`, {mode:'cors'});
            const parsed = await response.json();

            this.weather = {timestamp: await parsed.observations[0].obsTimeUtc,
                            otemp: await parsed.observations[0].imperial.temp,
                            oheat: await parsed.observations[0].imperial.heatIndex,
                            ochill: await parsed.observations[0].imperial.windChill,
                            odew: await parsed.observations[0].imperial.dewpt,
                            orh: await parsed.observations[0].humidity,
                            bar: await parsed.observations[0].imperial.pressure,
                            wspeed: await parsed.observations[0].imperial.windSpeed,
                            wspeedDir: await parsed.observations[0].winddir,
                            wgust: await parsed.observations[0].imperial.windGust,
                            rain: await parsed.observations[0].imperial.precipRate,
                            raintotal: await parsed.observations[0].imperial.precipTotal,
                            sr: await parsed.observations[0].solarRadiation,
                            uv: await parsed.observations[0].uv,
                            qc: await parsed.observations[0].qcStatus
                        };
        }

        weatherData(this.props.CONFIG.stations[this.props.page.station].wunderground, this.props.CONFIG.stations[this.props.page.station].wuApiKey);

        if (this.weather !== null) {
            return (
                <>
                <h1 className="text-capitalize">Weather At Friend's Station: {this.props.CONFIG.stations[this.props.page.station].name}</h1>
                <Row xs={2} md={3} lg={4} xl={6} className="g-4">
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Outdoor Temperature</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="otemp" current={this.weather.otemp} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Heat Index</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="oheat" current={this.weather.oheat} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Wind Chill</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="ochill" current={this.weather.ochill} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Dew Point</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="odew" current={this.weather.odew} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Humidity</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="orh" current={this.weather.orh} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Barometer</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="bar" current={this.weather.bar} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Wind Speed</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="wspeed" current={this.weather.wspeed} CONSTS={this.props.CONSTS}/>
                                <p className="text-white"><i>Wind Direction: {this.weather.wspeedDir}</i></p>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Wind Gust</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="wgust" current={this.weather.wgust} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Rain Rate</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="rain" current={this.weather.rain} CONSTS={this.props.CONSTS}/>
                                <p className="text-white"><i>Total (d1): {this.weather.raintotal}</i></p>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">Radiation</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="sr" current={this.weather.sr} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card>
                            <Card.Header className="bg-dark bg-gradient text-center text-white">UV</Card.Header>
                            <Card.Body className="bg-dark bg-opacity-75">
                                <WxMainObsCardCurrent obs="uv" current={this.weather.uv} CONSTS={this.props.CONSTS}/>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
                </>
            )
        } else {
            return (<></>);
        }
    }
}

export default WxFriendObservationPane