import React, { useState } from 'react';
import  Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

export function WxAqiModal(data) {
    const [show, setShow] = useState(false);

    let btnColor = 'bg-light';
    let aqiParsed = null;
    let tableRows = {};
    let obsTime = '';

    if (data.aqi instanceof Array) {

        aqiParsed = _parseAqi(data.aqi);

        if (aqiParsed !== null && aqiParsed.length > 0) {
            
            if (aqiParsed[0].Category.Number > 1) btnColor = "bg-aq-"+aqiParsed[0].Category.Name.replaceAll(' ', '-').toLowerCase();

            obsTime = `Observation Time: ${aqiParsed[0].HourObserved}:00 ${aqiParsed[0].LocalTimeZone} on ${aqiParsed[0].DateObserved}`;

            tableRows = aqiParsed.map((pol, i) => 
                <tr key={"aqi-tr"+i} className={"bg-aq-"+pol.Category.Name.replaceAll(' ', '-').toLowerCase()}>
                <td key={"aqi-td1"+i}>
                    {pol !== undefined && pol.rank}
                </td>
                <td key={"aqi-td2"+i}>
                    {pol !== undefined && pol.ParameterName}
                </td>
                <td key={"aqi-td3"+i}>
                    {pol !== undefined && pol.AQI}
                </td>
            </tr>
            );
        }
    }
    return (
        <>
            <Button size="sm" onClick={() => setShow(true)} className={btnColor + " bg-opacity-75 text-black border-dark"}>
                AQI
            </Button>
            <Modal
                size="sm"
                show={show}
                onHide={() => setShow(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="aqiModal">
                        AQI Information
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Table bordered={false} size="sm">
                        <tbody>
                            {tableRows}
                        </tbody>
                    </Table>
                    <p><small><em>{obsTime}</em></small></p>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

function _parseAqi(aqi) {

    let sorted = aqi.sort(function(x,y) {
        if (x.AQI > y.AQI) return -1;
        if (x.AQL < y.AQL) return 1;
        return 0;
    })

    for (let i = 0; i < sorted.length; i++) {
        if (i===0) sorted[i].rank = 'Primary';
        if (i===1) sorted[i].rank = 'Secondary';
        if (i > 1) sorted[i].rank = '';
    }

    return sorted;
}
export default WxAqiModal;