import React from 'react';

import ButtonGroup from 'react-bootstrap/ButtonGroup';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';

import WxAqiModal from './wxmodals/wxaqimodal';
import WxAdminMsgModal from './wxmodals/wxadminmsgmodal';
import WxRadarModal from './wxmodals/wxradarmodal';
import WxImageModal from './wxmodals/wximagemodal';
import WxForecastModal from './wxmodals/wxforecastmodal';
import WxAboutAlmanacModal from './wxmodals/wxaboutalmanacmodal';
import WxLinksModal from './wxmodals/wxlinksmodal';

export class WxPageModalBar extends React.Component {

    render() {

        return(
            <ButtonToolbar className='bg-dark bg-gradient'>
                <ButtonGroup className="m-1">
                    <WxAqiModal aqi={this.props.aqi}/>
                </ButtonGroup>
                <ButtonGroup className="m-1">
                    <WxAdminMsgModal msg={this.props.CONFIG.adminMessage}/>
                </ButtonGroup>
                <ButtonGroup className="m-1">
                    <WxRadarModal station={this.props.page.station} CONFIG={this.props.CONFIG}/>
                    <WxImageModal id="satBtn" buttonTxt="Satellite" title="Satellite Image" src={this.props.CONFIG.satelliteMap.src}/>
                    <WxImageModal id="goesBtn" buttonTxt="GOES" title="GOES Satellite Image" src={this.props.CONFIG.satelliteMap.goesSrc}/>
                    <WxImageModal id="nationalBtn" buttonTxt="National" title="National Forecast Map" src={this.props.CONFIG.nationalMap + "?" + new Date().getTime()}/>
                    <WxForecastModal key={this.props.page.station} station={this.props.page.station} CONFIG={this.props.CONFIG}/>
                </ButtonGroup>
                <ButtonGroup className="m-1">
                    <WxAboutAlmanacModal id="aboutBtn" type="about" buttonTxt="About" title="About This Station" station={this.props.page.station} weather={this.props.weather} CONFIG={this.props.CONFIG}/>
                    <WxAboutAlmanacModal id="almanacBtn" type="almanac" buttonTxt="Almanac" title="Station Almanac" station={this.props.page.station} weather={this.props.weather} CONFIG={this.props.CONFIG}/>
                    <WxLinksModal id="linkBtn" station={this.props.page.station} CONFIG={this.props.CONFIG}/>
                </ButtonGroup>
            </ButtonToolbar>
        )
    }
}
export default WxPageModalBar;