import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

export function WxAdminMsgModal(data) {
    const [show, setShow] = useState(false);

    let btnClassList = 'd-none bg-light';

    if (data.msg.visible) btnClassList = 'd-block bg-' + data.msg.variant;

    return (
        <>
            <Button size="sm" onClick={() => setShow(true)} className={btnClassList + " bg-opacity-75 text-black border-dark"}>
                Admin
            </Button>
            <Modal
                size="sm"
                show={show}
                onHide={() => setShow(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="aqiModal">
                        Administrative Message
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {data.msg.message}
                </Modal.Body>
                <Modal.Footer>
                    <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
export default WxAdminMsgModal;