import React from "react";
import Alert from "react-bootstrap/Alert";
import {getColorData} from '../../util/wxcolorutils';

class WxWagObservation extends React.Component {

    render() {
        let colorData = getColorData(this.props.obs, this.props.weather.current, this.props.CONSTS.colorRanges);
        let obsStyle = {backgroundColor: colorData.bg.toString(), color: colorData.fg.toString()};

        let currentVal = parseFloat(this.props.weather.current.toFixed(2));
        if (this.props.obs === 'wspeed') {
            currentVal = `${this.props.weather.current} (From: ${this.props.weather.add})`;
        } else if (this.props.obs === 'wgust') {
            currentVal = `${this.props.weather.current} (From: ${this.props.weather.add})`;
        } else if (this.props.obs === 'rain') {
            currentVal = `${this.props.weather.current} (24h: ${this.props.weather.add})`;
        }

        return (
            <Alert className="p-0 m-1 d-flex justify-content-between" style={obsStyle}>
                <p className="mt-0 mb-0 ms-1 me-0">{this.props.weather.label}</p>
                <p className="mt-0 mb-0 ms-0 me-1">{currentVal}</p>
            </Alert>
        )
    }
}
export default WxWagObservation