import React, { useState } from 'react';
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

export function WxLinksModal(data) {
    const [show, setShow] = useState(false);

    let linkList = [];
    let station = data.CONFIG.stations[data.station];

    if (station.wunderground !== null) {
        linkList.push({url: `http://www.wunderground.com/personal-weather-station/dashboard?ID=${station.wunderground}`, label: `Weather Underground (${station.wunderground})`});
    }
    if (station.aprs !== null) {
        linkList.push({url: `http://www.aprs.fi/weather/a/${station.aprs}`, label: `APRS (${station.aprs})`});
    }
    if (station.pws !== null) {
        linkList.push({url: `http://pwsweather.com/station/pws/${station.pws}`, label: `Personal Weather Station (${station.pws})`});
    }
    if (station.weatherCloud !== null) {
        linkList.push({url: `https://app.weathercloud.net/${station.weatherCloud}#profile`, label: `Weather Cloud (${station.weatherCloud})`});
    }
    if (station.purpleAir !== null) {
        linkList.push({url: `${station.purpleAir}`, label: `Purple Air Map`});
    }
    if (station.fa !== null) {
        linkList.push({url: `${station.fa}`, label: `FlightAware Site Map`});
    }
    for (const l in data.CONFIG.siteLinks) {
        linkList.push({url: data.CONFIG.siteLinks[l].url, label: data.CONFIG.siteLinks[l].name});
    }

    let links = _createLinks(linkList);

    return (
        <>
            <Button size="sm" onClick={() => setShow(true)} className="bg-light bg-opacity-75 text-black border-dark">
                Links
            </Button>
            <Modal
                show={show}
                onHide={() => setShow(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="listModal">
                        Station Links
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <ListGroup>
                        {links}
                    </ListGroup>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

function _createLinks(linkList) {

    let items = [];

    for (var l = 0; l < linkList.length; l++) {
        if (l%2 === 0) {
            items.push(React.createElement(ListGroup.Item, {key: 'al'+l, className: "m-0 p-0 ps-1 pe-1"}, React.createElement('a', {className:"text-reset", href: linkList[l].url, target: "_blank", rel:"noreferrer"}, linkList[l].label)));
        } else {
            items.push(React.createElement(ListGroup.Item, {key: 'al'+l, className: "bg-secondary bg-opacity-25 m-0 p-0 ps-1 pe-1"}, React.createElement('a', {className:"text-reset", href: linkList[l].url, target: "_blank", rel:"noreferrer"}, linkList[l].label)));
        }
    }

    return items;
}
export default WxLinksModal;