import React from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import 'bootstrap/dist/css/bootstrap.min.css';
import './css/w8agt.css';
import './css/weather-icons.css';
import './css/weather-icons-wind.css';

import WxHeaderContainer from "./components/wxheadercontainer";
import WxFooterContainer from "./components/wxfootercontainer";
import WxAlertCard from "./components/wxalertcard";
import WxWagCard from "./components/wxwagcard";
import WxNavigation from './components/wxnavigation';
import WxMainWindow from './components/wxmainwindow';
import WxAdminMessageBanner from './components/wxadminmessagebanner';
import WxPageModalBar from './components/wxpagemodalbar';

import { ALERT_LOCATIONS } from "./config/wxalertconfig";

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {page: this.props.page,
                      alerts: {},
                      aqi: {},
                      weather: {}};

        this.handleSelect = this.handleSelect.bind(this);
    }

    componentDidMount() {
        this._refreshWeather();

        this.refreshTimer = setInterval(() => this._refreshWeather(), this.props.CONFIG.defaultRefresh * 1000);
    }

    componentWillUnmount() {
        clearInterval(this.refreshTimer);
    }

    _refreshWeather() {

        if (this.state.page.page === this.props.CONSTS.viewOpts.page.sysAlert) return;

        this.wxStation = this._findClosest();

        //console.log(this.state.page);

        // Weather Data
        const weatherData = async () => {
            const response = await fetch(`https://weather.russellv2.com/${this.wxStation.station}/wx7.json`, {mode:'cors'});
            const parsed = await response.json();

            this.setState({weather: parsed});
        }

        // AQI
        const aqiData = async () => {
            const response = await fetch(this.props.CONFIG.aqiPath);
            const parsed = await response.text();
            this.setState({aqi: JSON.parse(parsed)});
        }

        // Alerts
        const alertData = async () => {
            let alertZones = '';

            for (const loc in ALERT_LOCATIONS) {
                alertZones += `${ALERT_LOCATIONS[loc].county},${ALERT_LOCATIONS[loc].zone},`;
            }
            alertZones = alertZones.substring(0, alertZones.length - 1);

            //const response = await fetch("https://api.weather.gov/alerts/active/area/MI", {mode:'cors'});
            const response = await fetch("https://api.weather.gov/alerts/active?zone="+alertZones, {mode:'cors'});
            const parsed = await response.json();

            this.setState({alerts: parsed});
        }

        weatherData();
        aqiData();
        alertData();
    }

    handleSelect(eventKey) {
        if (Object.keys(this.props.CONSTS.viewOpts.page).includes(eventKey)) {
            let newPage = this.state.page;
            newPage.page = eventKey;
            this.setState({page: newPage});
        } else if (Object.keys(this.props.CONFIG.stations).includes(eventKey)) {
            let newPage = this.state.page;
            newPage.station = eventKey;
            if (this.props.CONFIG.stations[eventKey].status !== "activeFull" && this.props.CONFIG.stations[eventKey].status !== 'activeWx') {
                if (this.props.CONFIG.stations[eventKey].status === "friend") {
                    //console.log("Status: friend-zone");
                    newPage.page = this.props.CONSTS.viewOpts.page.siteObs;
                } else {
                    newPage.page = this.props.CONSTS.viewOpts.page.siteCam;
                }
            } else {
                newPage.page = this.props.CONSTS.viewOpts.page.siteObs;
            }
            this.setState({page: newPage});
            this._refreshWeather();
        } else if (Object.keys(this.props.CONSTS.viewOpts.options).includes(eventKey)) {
            let newPage = this.state.page;
            newPage.option = eventKey;
            this.setState({page: newPage});
        } else if (eventKey === 'homeNavBtn') {
            let newPage = {station: "rockford",
                          page: this.props.CONSTS.viewOpts.page.siteObs,
                          option: this.props.CONSTS.viewOpts.options.h24};
            this.setState({page: newPage});
            this.wxStation = this._findClosest();
            this._refreshWeather();
            this.forceUpdate();
        } else if (eventKey === 'refreshNavBtn') {
            this._refreshWeather();
            this.forceUpdate();
        }
    }

    render() {

        this.wxStation = this._findClosest();

        if (this.state.weather.details === undefined) this.headerTimestamp = null; else this.headerTimestamp = this.state.weather.details.timestamp;

        return (
            <div>
                <WxHeaderContainer page={this.state.page} timestamp={this.headerTimestamp} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>
                <Container fluid>
                    <Row>
                        <Col xs={12} lg={6} className="ps-0 pe-0 ms-0 me-0">
                            <WxAlertCard alerts={this.state.alerts} station={this.state.page.station} CONFIG={this.props.CONFIG}/>
                        </Col>
                        <Col xs={12} lg={6} className="ps-0 pe-0 ms-0 me-0">
                            <WxWagCard page={this.state.page} station={this.wxStation} weather={this.state.weather} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>
                        </Col>
                    </Row>
                </Container>
                <WxAdminMessageBanner CONFIG={this.props.CONFIG.adminMessage}/>
                <WxPageModalBar page={this.state.page} aqi={this.state.aqi} weather={this.state.weather} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>
                <WxNavigation page={this.state.page} handleChangeProps={this.handleSelect} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>
                <WxMainWindow page={this.state.page} weather={this.state.weather} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>
                <WxFooterContainer CONSTS={this.props.CONSTS.siteMeta}/>
            </div>
        )
    }

    _findClosest() {
        let lat1 = this.props.CONFIG.stations[this.state.page.station].lat;
        let lon1 = this.props.CONFIG.stations[this.state.page.station].lon;
        const R = 6371e3;
        
        if (this.props.CONFIG.stations[this.state.page.station].status === 'activeFull' || this.props.CONFIG.stations[this.state.page.station].status === 'activeWx') {
            return {station: this.state.page.station, distance: 0};
        } else {
            var staname = [];
            var stadistance = [];
            for (const sta in this.props.CONFIG.stations) {
                var lat2, lon2;
                if (this.props.CONFIG.stations[sta].status === 'activeFull' || this.props.CONFIG.stations[sta].status === 'activeWx') {
                    lat2 = this.props.CONFIG.stations[sta].lat;
                    lon2 = this.props.CONFIG.stations[sta].lon;
    
                    const rad1 = lat1 * Math.PI/180;
                    const rad2 = lat2 * Math.PI/180;
                    const deltalat = (lat2-lat1) * Math.PI/180;
                    const deltalon = (lon2-lon1) * Math.PI/180;
    
                    const a = Math.sin(deltalat/2) * Math.sin(deltalat/2) +
                        Math.cos(rad1) * Math.cos(rad2) *
                        Math.sin(deltalon/2) * Math.sin(deltalon/2);
                    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
                    const d = R * c;
                    staname.push(sta);
                    stadistance.push(d/1000);
                }
            }
            var index = stadistance.indexOf(Math.min.apply(Math, stadistance));
            return {station: staname[index], distance: Math.min.apply(Math, stadistance).toFixed(1)};
        }
    }
}
export default App