import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import Chart from 'react-google-charts';

export function WxGraphModal(data) {
    const [show, setShow] = useState(false);

    return (
        <>
            <Button size="sm" onClick={() => setShow(true)}  className="bg-light bg-opacity-75 text-black border-dark">
                {data.buttonTxt}
            </Button>
            <Modal
                size="lg"
                show={show}
                onHide={() => setShow(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="imageModal" className="text-capitalize">
                        {data.title}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Chart
                        chartType='LineChart'
                        width="100%"
                        height="400px"
                        data={data.series}
                        options={data.options}
                    />
                </Modal.Body>
                <Modal.Footer>
                    <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
export default WxGraphModal;