// React and PropType Imports
import React from 'react';
import PropTypes from 'prop-types';

// React-Bootstrap Imports
import Container from 'react-bootstrap/Container';

// Wx Component Imports
import WxObservationPane from './mainwindowpanes/wxobservationpane';
import WxFriendObservationPane from './mainwindowpanes/wxfriendobservationpane';
import WxCameraPane from './mainwindowpanes/wxcamerapane';
import WxImageArchivePane from './mainwindowpanes/wximagearchivepane';
import WxSystemCameraPane from './mainwindowpanes/wxsystemcamerapane';
import WxSystemMapPane from './mainwindowpanes/wxsystemmappane';
import WxSystemSummaryPane from './mainwindowpanes/wxsystemsummarypane';
import WxAlertLogPane from './mainwindowpanes/wxalertlogpane';
import WxNoaaPane from './mainwindowpanes/wxnoaapane';

/**
 * Class to display the MainPage Element
 * 
 * Displays the main content page on the main page
 * 
 *      Extends: React.Component
 *      Main Component: Bootstrap Container Component
 * 
 *      Properties:
 *          page - which page to display
 *          weather - weather data to display on pages that require it
 *          CONFIG - wxconfig.json file contents
 *          CONSTS - wxconstantlib.json file contents
 */
class WxMainWindow extends React.Component {

    /**
     * Renders the component to the screen
     * @returns React Compononet
     */
    render() {
        
        return (
            <Container>
                {this._getChildWindow()}
            </Container>
        )
    }

    /**
     * Determine window to display within this component based upon page data
     * @returns Wx Component to Render on screen
     */
    _getChildWindow() {
        if (this.props.page.page === this.props.CONSTS.viewOpts.page.siteObs) {
            if (this.props.CONFIG.stations[this.props.page.station].status !== 'friend') {
                return (<WxObservationPane page={this.props.page} weather={this.props.weather} CONSTS={this.props.CONSTS}/>)
            } else {
                return (<WxFriendObservationPane page={this.props.page} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>)
            }
        } else if (this.props.page.page === this.props.CONSTS.viewOpts.page.siteCam) {
            return (<WxCameraPane page={this.props.page} CONFIG={this.props.CONFIG}/>)
        } else if (this.props.page.page === this.props.CONSTS.viewOpts.page.siteImg) {
            return (<WxImageArchivePane page={this.props.page} CONFIG={this.props.CONFIG}/>)
        } else if (this.props.page.page === this.props.CONSTS.viewOpts.page.siteNoaa) {
            return (<WxNoaaPane page={this.props.page} CONFIG={this.props.CONFIG}/>)
        } else if (this.props.page.page === this.props.CONSTS.viewOpts.page.sysAlert) {
            return (<WxAlertLogPane CONFIG={this.props.CONFIG}/>)
        } else if (this.props.page.page === this.props.CONSTS.viewOpts.page.sysSumm) {
            return (<WxSystemSummaryPane page={this.props.page} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>)
        } else if (this.props.page.page === this.props.CONSTS.viewOpts.page.sysMap) {
            return (<WxSystemMapPane page={this.props.page} CONFIG={this.props.CONFIG}/>)
        } else if (this.props.page.page === this.props.CONSTS.viewOpts.page.sysImgs) {
            return (<WxSystemCameraPane CONFIG={this.props.CONFIG}/>)
        }
    }
}
export default WxMainWindow;

WxMainWindow.propTypes = {
    page: PropTypes.object.isRequired,
    weather: PropTypes.object.isRequired,
    CONFIG: PropTypes.object.isRequired,
    CONSTS: PropTypes.object.isRequired
}