import React, { useState } from 'react';

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Image from 'react-bootstrap/Image';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ProgressBar from 'react-bootstrap/ProgressBar';

export function WxAboutAlmanacModal(data) {
    const [show, setShow] = useState(false);

    let modalData = _parseData(data.type, data);

    return (
        <>
            <Button size="sm" onClick={() => setShow(true)} className="bg-light bg-opacity-75 text-black border-dark">
                {data.buttonTxt}
            </Button>
            <Modal
                show={show}
                onHide={() => setShow(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="aboutAlmanacModal">
                        {data.title}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {modalData}
                </Modal.Body>
                <Modal.Footer>
                    <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

function _parseData(type, data) {
    if (data.weather.details !== undefined) {
        if (type === 'about') {
            return (
                <Container fluid>
                    <Row>
                        <Col>
                            <h4 className="text-center">Physical Location</h4>
                            <p>
                                <strong>Latitude:</strong><br/> {data.weather.details.lat}<br/><br/>
                                <strong>Longitude:</strong><br/> {data.weather.details.lon}<br/><br/>
                                <strong>Elevation:</strong> {data.weather.details.el}
                            </p>
                            <h4 className="text-center">Hardware Details</h4>
                            <p>
                                <strong>Hardware:</strong> {data.weather.details.hw}<br/>
                                <strong>Software Version:</strong> {data.weather.details.weev}<br/>
                                <strong>Software Uptime:</strong><br/> {data.weather.details.weeup}<br/><br/>
                                <strong>Server Uptime:</strong><br/> {data.weather.details.osup}
                            </p>
                        </Col>
                        <Col>
                            <h4 className="text-center">Station Photo</h4>
                            <Image src={`${data.CONFIG.basePath}/${data.station}/station.jpg`} fluid/>
                        </Col>
                    </Row>
                </Container>
            )
        } else if (type === 'almanac') {
            let moonEvents = null;
            let calEvents = null;

            if (new Date(data.weather.almanac.moon.nextEvents.nextNew).getTime() < new Date(data.weather.almanac.moon.nextEvents.nextFull).getTime()) {
                moonEvents = (<>
                    <strong>Next New Moon:</strong><br/> {data.weather.almanac.moon.nextEvents.nextNew}<br/><br/>
                    <strong>Next Full Moon:</strong><br/> {data.weather.almanac.moon.nextEvents.nextFull}<br/><br/>
                </>)
            } else {
                moonEvents = (<>
                    <strong>Next Full Moon:</strong><br/> {data.weather.almanac.moon.nextEvents.nextFull}<br/><br/>
                    <strong>Next New Moon:</strong><br/> {data.weather.almanac.moon.nextEvents.nextNew}<br/><br/>
                </>)
            }

            if (new Date(data.weather.almanac.other.solstice).getTime() < new Date(data.weather.almanac.other.equinox).getTime()) {
                calEvents = (<>
                    <strong>Next Solstice:</strong> {data.weather.almanac.other.solstice}<br/>
                    <strong>Next Equinox:</strong> {data.weather.almanac.other.equinox}
                </>)
            } else {
                calEvents = (<>
                    <strong>Next Equinox:</strong> {data.weather.almanac.other.equinox}<br/>
                    <strong>Next Solstice:</strong> {data.weather.almanac.other.solstice}
                </>)
            }

            return (
                <Container fluid>
                    <Row>
                        <Col>
                            <h4 className="text-center">Solar</h4>
                            <p>
                                <strong>Start Twilight:</strong> {data.weather.almanac.twilight.start}<br/>
                                <strong>Sunrise:</strong> {data.weather.almanac.sun.rise}<br/>
                                <strong>Sun Transit:</strong> {data.weather.almanac.sun.transit}<br/>
                                <strong>Sunset:</strong> {data.weather.almanac.sun.set}<br/>
                                <strong>End Twilight:</strong> {data.weather.almanac.twilight.end}
                            </p>
                        </Col>
                        <Col>
                            <h4 className="text-center">Lunar</h4>
                            <p>
                                <strong>Moonrise:</strong> {data.weather.almanac.moon.rise}<br/>
                                <strong>Moon Transit:</strong> {data.weather.almanac.moon.transit}<br/>
                                <strong>Moonset:</strong> {data.weather.almanac.moon.set}<br/>
                                <strong>Moon Phase:</strong> {data.weather.almanac.moon.phase} ({data.weather.almanac.moon.full}%)<br/>
                            </p>
                                <ProgressBar variant="warning" className="bg-dark" now={data.weather.almanac.moon.full}/>
                            <p>
                                {moonEvents}
                            </p>
                        </Col>
                    </Row>
                    <Row>
                        <h4 className="text-center">Other Events</h4>
                        {calEvents}
                    </Row>
                </Container>
            )
        }
    }
    
    return (<p>Loading...</p>);
}
export default WxAboutAlmanacModal;