import React from 'react';

import WxMapComponent from '../wxmapcomponent';

export class WxSystemMapPane extends React.Component {
    
    render() {

        return(
            <div style={{width: "100%", height: "500px"}}>
                <WxMapComponent id="wagMap" station={this.props.page.station} radarVisible={true} stationVisible={true} CONFIG={this.props.CONFIG} showAllStations={true} legendVisible={true}/>
            </div>
        )
    }
}
export default WxSystemMapPane