import React from 'react';

import * as ColorUtils from '../../../util/wxcolorutils';

export class WxMainObsCardCurrent extends React.Component {

    render() {

        const gaugeLimits = {min: 0, max: 96};
        const gaugeRange = ColorUtils.getObsRange(this.props.obs, this.props.CONSTS.colorRanges);
        const gaugePct = ColorUtils.findScaleRelativePosition(this.props.current, gaugeRange.range, gaugeLimits);

        const colorData = ColorUtils.getColorData(this.props.obs, this.props.current, this.props.CONSTS.colorRanges);

        let gaugeStyle = {};
        gaugeStyle['--p'] = gaugePct;
        gaugeStyle['--clr'] = `${colorData.bg.toString()}`;
        gaugeStyle['--b'] = '10px';

        return (
            <div style={{width: "120px"}}>
                <h5 className="text-white fw-bold">Now:</h5>
                <div className="pie text-white" style={gaugeStyle}>
                    {this.props.current.toFixed(1).toString()}
                </div>
            </div>
        )
    }
}
export default WxMainObsCardCurrent;