import React from 'react';
import Form from 'react-bootstrap/Form';
import {getIndexedFilenames} from '../../util/wxcolorutils.js';

export class WxNoaaPane extends React.Component {

    constructor(props) {
        super(props);

        this.state = {files: null,
            contents: null};
    }

    componentDidMount() {
        this.files = this._getDirContents(this.props.CONFIG.stations[this.props.page.station].noaaDir)
    }

    // Reads specific NOAA file
    async _getFileContents(file) {

        // Get Files
        const getContents = async (file) => {
            let response = await fetch(file, {mode:'cors'});
            let parsedText = await response.text();

            return await parsedText;
        }

        this.setState({contents: await getContents(this.props.CONFIG.stations[this.props.page.station].noaaDir + '/' + file)});
    }

    // Reads directory of files (for dropdown)
    async _getDirContents(file) {

        this.setState({files:  await getIndexedFilenames(file, 'txt')});
    }

    render() {

        return (
            <>
                <Form.Group>
                    <Form.Label>Select NOAA File</Form.Label>
                    <Form.Select onChange={(e) => this._getFileContents(e.target.value)}>
                        { (this.state.files !== null) && 
                            Object.keys(this.state.files).map((file, i) => (
                                <option key={i} value={this.state.files[file]}>{this.state.files[file]}</option>
                            ))
                        }
                    </Form.Select>
                </Form.Group>
                { (this.state.contents !== null) && 
                    <p className="font-monospace" style={{fontFamily: 'monospace', whiteSpace: 'pre'}}>
                        {this.state.contents}
                    </p>
                }
            </>
        )
    }
}
export default WxNoaaPane;