import React from 'react';

import Chart from 'react-google-charts';

export class WxSummaryCardGraph extends React.Component {
    render() {

        let dataTable = [];

        let headers = [];
        headers[0] = "";
        let maxRow = [];
        maxRow[0] = "Max";
        let currentRow = [];
        currentRow[0] = "Current";
        let minRow = [];
        minRow[0] = "Min";

        for (const station in this.props.weather) {
            this.props.weather[station][this.props.field].current !== null && headers.push(station);
            this.props.weather[station][this.props.field].max !== null && maxRow.push(this.props.weather[station][this.props.field].max);
            this.props.weather[station][this.props.field].current !== null && currentRow.push(this.props.weather[station][this.props.field].current);
            this.props.weather[station][this.props.field].min !== null && minRow.push(this.props.weather[station][this.props.field].min);
        }
        dataTable.push(headers);
        dataTable.push(maxRow);
        dataTable.push(currentRow);
        dataTable.push(minRow);

        return (
            <Chart 
            chartType="BarChart"
            width="100%"
            height="400px"
            data={dataTable}
            />
        );
    }
}
export default WxSummaryCardGraph;