import React from 'react';

import Alert from 'react-bootstrap/Alert';
import Offcanvas from 'react-bootstrap/Offcanvas';
import Button from 'react-bootstrap/Button';
import ListGroup from 'react-bootstrap/ListGroup';
import Stack from 'react-bootstrap/Stack';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';

import { ALERT_EVENTS, ALERT_LOCATIONS } from '../../config/wxalertconfig';

export class WxAlertCardOffcanvas extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isShown: false,
            variant: this.props.variant
        }

        this.handleButtonClick = this.handleButtonClick.bind(this);
    }

    handleButtonClick() {
        this.setState({isShown: !this.state.isShown});
    }

    handleHide() {
        this.setState({isShown: false});
    }

    _findVariant(alert) {
        if (alert.indexOf('Warning') !== -1) {
            // Is it an "Extreme" event?
            return 'danger';
        } else if (alert.indexOf('Watch') !== -1) {
            return 'warning';
        } else {
            return 'info';
        }
    }

    render() {

        let locationListItems = Object.keys(ALERT_LOCATIONS).map((loc, i) => (
                <Alert key={'aloc' + loc} className="p-0 m-1 d-flex bg-light">
                    {ALERT_LOCATIONS[loc].filter === null && ALERT_LOCATIONS[loc].location}
                    {ALERT_LOCATIONS[loc].filter !== null && ALERT_LOCATIONS[loc].location + ` (filtered)`}
                </Alert>
        ))

        let eventListItems = Object.keys(ALERT_EVENTS).map((loc, i) => (
            <Alert key={'aloc' + loc} variant={this._findVariant(loc)} className="p-0 m-1 d-flex justify-content-between">
                <p className="mt-0 mb-0 ms-1 me-0">{loc}</p><p className="mt-0 mb-0 ms-0 me-1"><i className={ALERT_EVENTS[loc]}/></p>
            </Alert>
        ))
        return (
            <>
                <Button className="ps-1 pe-1 pt-0 pb-0 m-0 border-none text-white float-end" variant={this.state.variant} onClick={this.handleButtonClick}>
                    <i className="fa fa-info-circle"/>
                </Button>
                <Offcanvas show={this.state.isShown} onHide={this.handleButtonClick}>
                    <Offcanvas.Header closeButton>
                        <Offcanvas.Title>Alert Events/Locations</Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                        <Tabs defaultActiveKey="alertLocs" id="alertLocsTabs">
                            <Tab eventKey="alertLocs" title="Locations">
                                <ListGroup>
                                    {locationListItems}
                                </ListGroup>
                            </Tab>
                            <Tab eventKey="alertEvents" title="Events">
                                <Stack gap={0}>
                                    {eventListItems}
                                </Stack>
                            </Tab>
                        </Tabs>
                    </Offcanvas.Body>
                </Offcanvas>
            </>
        );
    }
}
export default WxAlertCardOffcanvas;