import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Image from 'react-bootstrap/Image';

export function WxImageModal(data) {
    const [show, setShow] = useState(false);

    return (
        <>
            <Button size="sm" onClick={() => setShow(true)}  className="bg-light bg-opacity-75 text-black border-dark">
                {data.buttonTxt}
            </Button>
            <Modal
                size="lg"
                show={show}
                onHide={() => setShow(false)}
            >
                <Modal.Header closeButton>
                    <Modal.Title id="imageModal" className="text-capitalize">
                        {data.title}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Image src={data.src} fluid/>
                </Modal.Body>
                <Modal.Footer>
                    <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}
export default WxImageModal;