import React from 'react';

import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
import Modal from 'react-bootstrap/Modal';

class WxHeaderCarousel extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            show: false
        }
    }
    render() {

        return (
            <>
                <Carousel controls={false} indicators={false} pause="hover" onClick={e=> this.setState({show: true})}>
                    { Object.keys(this.props.station.image.list).map((cam, i) => (
                        <Carousel.Item key={i}>
                            <img key={i} className="d-block w-100" src={this.props.station.image.list[cam] + '?' + new Date().getTime()} alt={cam}/>
                        </Carousel.Item>))
                    }
                </Carousel>
                <Modal show={this.state.show} onHide={() => this.setState({show: false})}>
                    <Modal.Header closeButton>
                        <Modal.Title>
                            Station Camera Images
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Carousel controls={true} indicators={true}>
                            { Object.keys(this.props.station.image.list).map((cam, i) => (
                                <Carousel.Item key={"m"+ i}>
                                    <img key={"m" + i} className="d-block w-100" src={this.props.station.image.list[cam] + '?' + new Date().getTime()} alt={cam}/>
                                </Carousel.Item>))
                            }
                        </Carousel>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={() => this.setState({show: false})}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
    }
}
export default WxHeaderCarousel;