import React from 'react';

import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import WxSummaryObsCard from "./systemsummarypanecomponents/wxsummaryobscard";

export class WxSystemSummaryPane extends React.Component {
    static FIELDS = ['otemp', 'oheat', 'ochill', 'odew', 'orh', 'bar', 'wspeed', 'wgust', 'rain', 'sr', 'uv', 'itemp', 'irh'];

    constructor(props) {
        super(props);

        this.state = {weather: {}};
        this.initLoading = true;
    }

    componentDidMount() {
        this._refreshWeather();
    }

    _refreshWeather = async () => {

        // Weather Data
        const weatherData = async (station) => {
            const response = await fetch(`https://weather.russellv2.com/${station}/wx7.json`, {mode:'cors'});
            const parsed = await response.json();

            return await parsed;
        }

        let wx = {};

        for (const station in this.props.CONFIG.stations) {
            if (this.props.CONFIG.stations[station].status === this.props.CONSTS.stationTypes.full || this.props.CONFIG.stations[station].status === this.props.CONSTS.stationTypes.wxOnly) {
               wx[station] = await weatherData(station);
            }
        }
        this.setState({weather: wx});
        this.initLoading = false;
    }

    render() {

        if (this.initLoading) {
            return (
                <Row className="row-cols-1 row-cols-sm-2 row-cols-lg-3 row-cols-xl-4">
                    <h2>Loading...</h2>
                </Row>
            )
        } else {

            let weather = this._parseWeather(this.state.weather);

            return (
                <Row className="row-cols-1 row-cols-sm-2 row-cols-lg-3 row-cols-xl-4">
                    {
                        WxSystemSummaryPane.FIELDS.map((field, i) => (
                            <Col className="pb-2 ps-1 pe-1" key={i}>
                                <WxSummaryObsCard key={i} field={field} weather={weather} CONFIG={this.props.CONFIG} CONSTS={this.props.CONSTS}/>
                            </Col>
                        ))
                    }
                </Row>
            )
        }
    }

    _parseWeather(weather) {

        let wx = {};

        for (const station in this.state.weather) {
            wx[station] = {};
            for (const field in WxSystemSummaryPane.FIELDS) {
                if (weather[station].observations[WxSystemSummaryPane.FIELDS[field]] !== undefined) {

                    let stats = {
                        title: weather[station].observations[WxSystemSummaryPane.FIELDS[field]].label,
                        min: weather[station].observations[WxSystemSummaryPane.FIELDS[field]].summary[this.props.page.option].min[1],
                        max: weather[station].observations[WxSystemSummaryPane.FIELDS[field]].summary[this.props.page.option].max[1],
                        avg: weather[station].observations[WxSystemSummaryPane.FIELDS[field]].summary[this.props.page.option].avg,
                        current: weather[station].observations[WxSystemSummaryPane.FIELDS[field]].current
                    }

                    wx[station][WxSystemSummaryPane.FIELDS[field]] = stats;
                }
            }
        }
        return wx;
    }
}
export default WxSystemSummaryPane;