import React from 'react';

import Offcanvas from 'react-bootstrap/Offcanvas';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';

import WxMapComponent from '../wxmapcomponent';

export class WxAlertInfoOffcanvas extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isShown: false,
            alert: props.alert
        }

        this.handleButtonClick = this.handleButtonClick.bind(this);
    }

    handleButtonClick() {
        this.setState({isShown: !this.state.isShown});
    }

    handleHide() {
        this.setState({isShown: false});
    }

    render() {
        let information = this.state.alert.properties.description.replace(/\n\n/g, "<br />");
        let instructions = ''
        if (this.state.alert.properties.instruction !== null) instructions = this.state.alert.properties.instruction.replace(/\n\n/g, "<br />");

        return (
            <>
                <Button className="ps-1 pe-1 pt-0 pb-0 m-0 border-dark" variant={this.state.alert.properties.severeColor} onClick={this.handleButtonClick}>
                    <i className={"fa fa-info-circle " + this.props.btnText}/>
                </Button>
                <Offcanvas show={this.state.isShown} onHide={this.handleButtonClick}>
                    <Offcanvas.Header className={'bg-'+this.state.alert.properties.severeColor} closeButton>
                        <Offcanvas.Title><i className={" me-2 " + this.state.alert.properties.icon}/>{this.state.alert.properties.event}</Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                        <Table bordered variant='dark' size="sm">
                            <tbody>
                                <tr>
                                    <td colSpan="4">{this.state.alert.properties.headline}</td>
                                </tr>
                                <tr>
                                    <td>Effective:</td><td>{new Date(this.state.alert.properties.effective).toLocaleString()}</td>
                                    <td>Start:</td><td>{new Date(this.state.alert.properties.onset).toLocaleString()}</td>
                                </tr>
                                <tr>
                                    <td>Expires:</td><td>{new Date(this.state.alert.properties.expires).toLocaleString()}</td>
                                    <td>Ends:</td><td>{new Date(this.state.alert.properties.ends).toLocaleString()}</td>
                                </tr>
                                <tr>
                                    <td colSpan="1">Type:</td><td colSpan="3">{this.state.alert.properties.messageType}</td>
                                </tr>
                                <tr>
                                    <td colSpan="1">Severity:</td><td colSpan="3">{this.state.alert.properties.severity}</td>
                                </tr>
                                <tr>
                                    <td colSpan="1">Status:</td><td colSpan="3">{this.state.alert.properties.status}</td>
                                </tr>
                                <tr>
                                    <td colSpan="1">Urgency:</td><td colSpan="3">{this.state.alert.properties.urgency}</td>
                                </tr>
                                <tr>
                                    <td colSpan="1">Certainty:</td><td colSpan="3">{this.state.alert.properties.certainty}</td>
                                </tr>
                                <tr>
                                    <td colSpan="4">Information:<br/><p dangerouslySetInnerHTML={{__html: information }}/></td>
                                </tr>
                                <tr>
                                    <td colSpan="4">Instructions:<br/><p dangerouslySetInnerHTML={{__html: instructions }}/></td>
                                </tr>
                                <tr>
                                    <td colSpan="4" style={{height: "200px"}}>
                                        <WxMapComponent
                                            id="alertMap"
                                            station={this.props.station}
                                            radarVisible={true}
                                            radarAnimate={true}
                                            stationVisible={true}
                                            warnVisible={true}
                                            polygons={this.props.alert.properties.polygonCoords}
                                            variant={this.props.alert.properties.severeColor}
                                            CONFIG={this.props.CONFIG}
                                        />
                                    </td>
                                </tr>
                            </tbody>
                        </Table>
                    </Offcanvas.Body>
                </Offcanvas>
            </>
        );
    }
}
export default WxAlertInfoOffcanvas;