import React from 'react';

export class WxHourlyForecastRow extends React.Component {

    render() {

        let day = 'bg-light';

        if (!this.props.forecast.isDaytime) day = 'bg-dark bg-opacity-25 text-dark';

        return(
            <tr className={day}>
                <td>{new Date(this.props.forecast.startTime).toLocaleDateString()}<br/>{new Date(this.props.forecast.startTime).toLocaleTimeString()}</td>
                <td><img src={this.props.forecast.icon} alt={this.props.forecast.shortForecast}/></td>
                <td>{this.props.forecast.temperature}{this.props.forecast.temperatureUnit}<br/>{this.props.forecast.windSpeed} from {this.props.forecast.windDirection}</td>
                <td>{this.props.forecast.shortForecast}</td>
            </tr>
            );
    }
}
export default WxHourlyForecastRow;