// React and PropTypes Imports
import React from "react";
import PropTypes from 'prop-types';

// React Bootstrap Imports
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Badge from "react-bootstrap/Badge";

// WX Component Imports
import WxHeaderCarousel from "./headercomponents/wxheadercarousel";

/**
 * Class to display the Header Element
 * 
 * Displays the Header Element on the main page.
 * 
 *      Extends: React.Component
 *      Main Component: React-Bootstrap Container Component
 * 
 *      Properties:
 *          page - page displayed information
 *          CONFIG - wxconfig.json file contents
 *          CONSTS - wxconstantlib.json file contents
 *          timestamp - timestamp from wx7 json file
 */
class WxHeaderContainer extends React.Component {
    render() {

        // Set timestamp as current timestamp by default
        this.timestamp = new Date().toLocaleString();
        // If we have timestamp as a property (from weather file) then use that as our timestamp
        if(this.props.timestamp !== undefined && this.props.timestamp !== null) this.timestamp = new Date(this.props.timestamp*1000).getTime();

        // Return React Component
        return (
            <Container id="WxHeader" className="text-center pb-4" fluid>
                <Row>
                    <Col xs={9} >
                        <h1>{this.props.CONFIG.stations[this.props.page.station].location}</h1>
                        <h2>{this._getPageTitle(this.props.page)}</h2>
                        <h4>{this._getPageSubtitle(this.props.page)}</h4>
                        <Badge bg="warning" text="dark" className={this._isStale(this.props.page.page)}>Stale Observation</Badge>
                    </Col>
                    <Col xs={3}>
                        { (this.props.CONFIG.stations[this.props.page.station].status === 'activeFull' || this.props.CONFIG.stations[this.props.page.station].status === 'activeCam') &&
                            <WxHeaderCarousel station={this.props.CONFIG.stations[this.props.page.station]}/>
                        }
                    </Col>
                </Row>
            </Container>
        )
    }

    /**
     * Sets the header title based upon the page displayed
     * @param {Object} title 
     * @returns {String} title
     */
    _getPageTitle(title) {
        if (title.page === this.props.CONSTS.viewOpts.page.siteObs) {
            // If this is an observation page, we need to include the duration as well
            if (title.option === this.props.CONSTS.viewOpts.options.h1) {
                return "Site Observations - Last 1 Hour";
            } else if (title.option === this.props.CONSTS.viewOpts.options.h24) {
                return "Site Observations - Last 24 Hours";
            } else if (title.option === this.props.CONSTS.viewOpts.options.d1) {
                return "Site Observations - Since Midnight";
            } else if (title.option === this.props.CONSTS.viewOpts.options.d7) {
                return "Site Observations - Last 7 Days";
            } else if (title.option === this.props.CONSTS.viewOpts.options.d30) {
                return "Site Observations - Last 30 Days";
            } else if (title.option === this.props.CONSTS.viewOpts.options.m1) {
                return "Site Observations - Since First of Month";
            } else if (title.option === this.props.CONSTS.viewOpts.options.d365) {
                return "Site Observations - Last 365 Days";
            } else if (title.option === this.props.CONSTS.viewOpts.options.y1) {
                return "Site Observations - Since January 1";
            } else if (title.option === this.props.CONSTS.viewOpts.options.at) {
                return "Site Observations - All Time";
            // Invalid duration
            } else return "Site Observations - Invalid Duration";
        } else if (title.page === this.props.CONSTS.viewOpts.page.siteCam) {
            return "Site Cameras";
        } else if (title.page === this.props.CONSTS.viewOpts.page.siteImg) {
            return "Site Image Archive";
        } else if (title.page === this.props.CONSTS.viewOpts.page.siteNoaa) {
            return "Site NOAA Report";
        } else if (title.page === this.props.CONSTS.viewOpts.page.sysAlerts) {
            return "System Alert Log";
        } else if (title.page === this.props.CONSTS.viewOpts.page.sysSumm) {
            return "System Summary";
        } else if (title.page === this.props.CONSTS.viewOpts.page.sysMap) {
            return "System Map";
        } else if (title.page === this.props.CONSTS.viewOpts.page.sysImgs) {
            return "System Image Collage";
        // Invalid page
        } else return "Invalid Page";
    }

    /**
     * Sets the header's subtitle based upon page selected
     * @param {Object} subtitle
     * @returns {String} subtitle
     */
    _getPageSubtitle(subtitle) {

        // If this is the site observation page than use the observed time
        if (subtitle.page === this.props.CONSTS.viewOpts.page.siteObs) {
            return "Observations Updated: "  + new Date(this.timestamp).toLocaleString();
        // Otherwise, use the refresh time
        } else return "Page Last Refreshed: " + new Date(this.timestamp).toLocaleString();
        
    }

    /**
     * Determines if observations are stale
     * 
     * @param {Object} page 
     * @returns {String} d-block or d-none to show or hide stale badge
     */
    _isStale(page) {
        // If we are looking at the site observations
        if (page === this.props.CONSTS.viewOpts.page.siteObs) {
            // Calculate stale time limit by subtracting limit from current time
            var staleLimit = new Date(new Date().getTime() - (this.props.CONFIG.staleObsThreshold * 1000));

            // If our observation time is older than the stale limit return d-block to show.
            // Otherwise, return d-none to hide
            if (new Date(this.timestamp).getTime() < staleLimit) return "d-block"; else return "d-none";

        // If we aren't looking at observations then return d-none to hide.
        } else return "d-none";
    }
}
export default WxHeaderContainer

WxHeaderContainer.propTypes = {
    page: PropTypes.object.isRequired,
    CONFIG: PropTypes.object.isRequired,
    CONSTS: PropTypes.object.isRequired,
    timestamp: PropTypes.number
}