// React and PropTypes Imports
import React from "react";
import PropTypes from 'prop-types';

// Bootstrap Imports
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import ListGroup from "react-bootstrap/ListGroup";

// WX Imports
import WxAlertSeparator from "./alertcardcomponents/wxalertseparator";
import WxAlertItem from "./alertcardcomponents/wxalertitem";
import WxAlertCardOffcanvas from "./wxoffcanvas/wxalertcardoffcanvas";
import WxAlertCardBadge from "./alertcardcomponents/wxalertcardbadge";
import { ALERT_EVENTS, ALERT_LOCATIONS } from "../config/wxalertconfig";
import { nwsLatLonFlip } from '../util/wxcolorutils';

/**
 * Class to display the AlertCard
 * 
 * Displays the Alert Card on the main page.
 * 
 *      Extends: React.Component
 *      Main Component: React-Bootstrap Card Component
 * 
 *      Properties:
 *          alerts - Alert json data from NWS API
 *          station - Station id
 *          CONFIG - wxconfig.json file contents          
 */
class WxAlertCard extends React.Component {

    /**
     * Renders the component to the screen
     * @returns React Component
     */
    render() {
        // Max severity level for alert list (determines card variant)
        this._maxSeverity = 0;
        // Initial card variant
        let variant = "success";
        // List of parsed alerts
        let alerts = {};
        // Number of alerts (for badge)
        let alertCount = 0;

        // If alerts have loaded then convert them to our format
        if (this.props.alerts !== undefined && this.props.alerts.features !== undefined) {
            alerts = this._convertAlerts(this.props.alerts.features);
            // If we have alerts then set the count
            if (alerts !== null) alertCount = alerts.length;
        }

        // Determine variant for card based upon max severity of alerts in list
        if (this._maxSeverity === 0) {
            variant = "success";
        } else if (this._maxSeverity === 1) {
            variant = "info";
        } else if (this._maxSeverity === 2) {
            variant = "warning";
        } else if (this._maxSeverity === 3 || this._maxSeverity === 4) {
            variant = "danger";
        } else {
            variant = "success";
        }

        // Build alert list from parsed alerts
        let alertList = this._buildAlertList(alerts);

        // Return the alert card
        return (
            <Card id="WxAlertCard" className="h-100" bg={variant.toLowerCase()} text={variant.toLowerCase() === 'light' ? 'dark': 'white'}>
                
                <Card.Header><h5>Weather Alerts<WxAlertCardBadge variant={variant.toLowerCase()} alertCount={alertCount}/><WxAlertCardOffcanvas variant={"bg-"+variant.toLowerCase()}/></h5></Card.Header>
                <Card.Body>
                    <Container fluid>
                        <Row>
                            <Col xs={8}>
                                <ListGroup as="ul">
                                    {alertList}
                                </ListGroup>
                            </Col>
                            <Col xs={4}>
                                <a href={this.props.CONFIG.alertMap.url + this.props.CONFIG.stations[this.props.station].nwsOffice} target="_blank" rel="noreferrer">
                                    <img className="img-fluid" src={this.props.CONFIG.alertMap.src + this.props.CONFIG.stations[this.props.station].nwsOffice + '.png?' + new Date().getTime()} alt="NWS Alert Map"/>
                                </a>
                            </Col>
                        </Row>
                    </Container>
                </Card.Body>
            </Card>
        );
    }

    /**
     * Converts and updates NWS API alert json to our format for use
     * @param {Object} alerts 
     * @returns {Object} sorted object containing alerts
     */
    _convertAlerts(alerts) {

        // Set our max severity to 0 to start
        let maxSeverity = 0;

        // If no alerts exit because nothing to do
        if (alerts === undefined || alerts.length === 0) return null;

        // Find unique alerts by ID
        let unique = [...new Map(alerts.map((item) => [item["properties"]["id"], item])).values()];

        // Loop through alerts
        for (let i=0; i<unique.length; i++) {
            unique[i].properties.group = '';
            unique[i].properties.polygonCoords = [];
            // Loop through locations
            for (let ii=0; ii<ALERT_LOCATIONS.length; ii++) {
                // If alert is for this location
                if (unique[i].properties.geocode.UGC.includes(ALERT_LOCATIONS[ii].county) || unique[i].properties.geocode.UGC.includes(ALERT_LOCATIONS[ii].zone)) {
                    // If we have a filter in place for this location
                    if (ALERT_LOCATIONS[ii].filter !== null) {
                        // If filter is included in alert event
                        if (ALERT_LOCATIONS[ii].filter.some(filter => unique[i].properties.event.includes(filter))) {
                            // Then we add the shown as location to the alert
                            unique[i].properties.group += `${ALERT_LOCATIONS[ii].shownAs} / `;
                            // If we have geometry from the NWS use it.  If not, add our location's polygon
                            if (unique[i].geometry !== null) {
                                unique[i].properties.polygonCoords.push(nwsLatLonFlip(unique[i].geometry.coordinates));
                            } else {
                                unique[i].properties.polygonCoords.push(ALERT_LOCATIONS[ii].mapCoords);
                            }
                        }
                    // If we don't have a filter then just add the alert
                    } else if (ALERT_LOCATIONS[ii].filter === null) {
                        unique[i].properties.group += `${ALERT_LOCATIONS[ii].shownAs} / `;
                        // f we have geometry from the NWS use it.  If not, add our location's polygon
                        if (unique[i].geometry !== null) {
                            unique[i].properties.polygonCoords.push(nwsLatLonFlip(unique[i].geometry.coordinates));
                        } else {
                            unique[i].properties.polygonCoords.push(ALERT_LOCATIONS[ii].mapCoords);
                        }
                    }
                }
            }
            // Prune extra slash at end of shown as
            unique[i].properties.group = unique[i].properties.group.substring(0, unique[i].properties.group.length - 3);
        }

        // Remove alerts without a 'group' field because they have been filtered out
        // Remove alerts that have expired more than alertExpDelay
        let expDate = this.props.CONFIG.alertExpDelay * 1000;
        let filtered = unique.filter(function(f) {return (f.properties.group !== '' && (new Date(f.properties.expires).getTime() + expDate) > new Date().getTime());});
        console.log(new Date(new Date().getTime() + expDate));
        // If we have filtered out all alerts, return
        if (filtered.length === 0) return null;

        // Loop through array to find severity
        for (let j=0; j<filtered.length; j++) {
            // Find event severity and keep track of most severe to color code
            // First is warning
            if (filtered[j].properties.event.indexOf('Warning') !== -1) {
                // Is it an "Extreme" event?
                if (filtered[j].properties.event.indexOf('Tornado') !== -1 || 
                    filtered[j].properties.event.indexOf('Hurricane') !== -1 ||
                    filtered[j].properties.event.indexOf('Flash Flood') !== -1 ||
                    filtered[j].properties.event.indexOf('Blizzard') !== -1) {
                        filtered[j].properties.severeLevel = 4;
                        maxSeverity = 4;
                } else {
                        filtered[j].properties.severeLevel = 3;
                        if (maxSeverity < 3) maxSeverity = 3;
                }
            // Next is watch
            } else if (filtered[j].properties.event.indexOf('Watch') !== -1) {
                filtered[j].properties.severeLevel = 2;
                if (maxSeverity < 2) maxSeverity = 2;
            // Statement, Advisory, Etc.
            } else {
                filtered[j].properties.severeLevel = 1;
                if (maxSeverity < 1) maxSeverity = 1;
            }

            // Find icon
            if (Object.keys(ALERT_EVENTS).includes(filtered[j].properties.event)) {
                filtered[j].properties.icon = ALERT_EVENTS[filtered[j].properties.event];
            } else {
                filtered[j].properties.icon = 'faQuestion';
            }
        }
        
        // Sort alerts (Location, Event, Start)
        let sorted = filtered.sort(function(x,y) {
            if (x.properties.group < y.properties.group) {
                return -1;
            } else if (x.properties.group > y.properties.group) {
                return 1;
            } else {
                if (x.properties.event < y.properties.event) {
                    return -1;
                } else if (x.properties.event > y.properties.event) {
                    return 1;
                } else{
                    if (new Date(x.properties.onset).getTime() < new Date(y.properties.onset).getTime()) {
                        return -1;
                    } else if (new Date(x.properties.onset).getTime() > new Date(y.properties.onset).getTime()) {
                        return 1;
                    } else {
                        return 0;
                    }
                }
            }
        });

        // Set max severity
        this._maxSeverity = maxSeverity;

        // Return our sorted list
        return sorted;
    }

    /**
     * Builds list of alerts + dividers for display in the card
     * @param {Object} alerts 
     * @returns {Array} String of React Elements (Dividers + Alerts)
     */
    _buildAlertList(alerts) {
        // Set initial location and 
        let alertLocation = '';
        let alertString = [];

        // If there are no alerts then create the "No Alerts" divider
        if (alerts === null) {
            alertString.push(React.createElement(WxAlertSeparator, {className: "text-center", key: alertLocation, text: 'NO CURRENT ALERTS'}));
        // Otherwise we need to loop through items to create list
        } else {
            for (const alert in alerts) {
                // If the alert's location doesn't match the previous then create a separator
                if (alerts[alert].properties.group !== alertLocation) {
                    // Set new location
                    alertLocation = alerts[alert].properties.group;
                    // Create separator
                    alertString.push(React.createElement(WxAlertSeparator, {key: alertLocation, text: alerts[alert].properties.group}));
                }
                // Add alert
                alertString.push(React.createElement(WxAlertItem, {key: alerts[alert].properties.id, alert: alerts[alert], station: this.props.station, CONFIG: this.props.CONFIG}));
            }
        }

        // Return
        return alertString;
    }
}
export default WxAlertCard;

// Prop Types Declaration
WxAlertCard.propTypes = {
    alerts: PropTypes.object.isRequired,
    station: PropTypes.string.isRequired,
    CONFIG: PropTypes.object.isRequired
};