import React from 'react';
//import { ThemeConsumer } from 'react-bootstrap/esm/ThemeProvider';

import WxGraphModal from '../../wxmodals/wxgraphmodal';

import Chart from 'react-google-charts';

export class WxMainObsCardGraph extends React.Component {

    render() {

        let vAxisMin = null;
        let vAxisMax = null;
        let hAxisFormat = "HH:mm";

        // If longer than 2-day span (48H), show dates rather than times.)
        if ((new Date(this.props.weather.series[this.props.weather.series.length - 1][0]).getTime() - new Date(this.props.weather.series[0][0]).getTime()) > (48*60*60*1000)) hAxisFormat = "MM/dd/yy";

        // Set y-min to 0 for orh, irh, wspeed, wgust, rain, sr, & uv
        if (this.props.obs === 'orh' || this.props.obs === 'wspeed' || this.props.obs === 'wgust' || this.props.obs === 'rain' ||
            this.props.obs === 'sr' || this.props.obs === 'uv' || this.props.obs === 'irh') vAxisMin = 0;
        
        // Set y-min and y-max for temperatures
        if (this.props.obs === 'otemp' || this.props.obs === 'itemp' || this.props.obs === 'ochill' ||
            this.props.obs === 'oheat' || this.props.obs === 'odew') {
            if (this.props.weather.records.min[1] < 0) {
                vAxisMin = Math.floor(this.props.weather.records.min[1] - 3)
            } else if (this.props.weather.records.min[1] < 32) {
                vAxisMin = 0;
            } else if (this.props.weather.records.min[1] < 50) {
                vAxisMin = 32;
            } else vAxisMin = 50;

            if (this.props.weather.records.max[1] > 100) {
                vAxisMax = Math.ceil(this.props.weather.records.max[1] + 3);
            } else if (this.props.weather.records.max[1] > 85) {
                vAxisMax = 100;
            } else vAxisMax = 85;
        
        // Set y-max to 1 for rain, wspeed, wgust if values are >1, otherwise set to 1
        } else if (this.props.obs === 'rain' || this.props.obs === 'wspeed' || this.props.obs === 'wgust') {
            if (this.props.weather.records.max[1] > 1) vAxisMax = Math.ceil(this.props.weather.records.max[1]); else vAxisMax = 1;
        }

        // Add labels
        if (this.props.weather.series[0][0] !== "Time") {
            // For most observations, just have time and value
            if (this.props.weather.series[0].length === 2) {
                this.props.weather.series.splice(0,0,[{format: 'date', label: "Time"}, this.props.weather.label]);
            } else {
                // For some observations (oheat && ochill) have time, otemp, and value
                if (this.props.obs === "oheat") {
                    this.props.weather.series.splice(0,0,[{format: 'date', label: "Time"}, "", this.props.weather.label]);
                } else if (this.props.obs === "ochill") {
                    this.props.weather.series.splice(0,0,[{format: 'date', label: "Time"}, this.props.weather.label, ""]);
                }
            }
        }

        // Set chart options
        const options = {
            title: this.props.weather.label,
            curveType: "function",
            legend: {position: "bottom"},
            vAxis: {
                viewWindow: {
                    min: vAxisMin,
                    max: vAxisMax
                },
            },
            hAxis: {
                format: hAxisFormat,
                slantedText: true
            }
        };
    
        // Render chart
        return (
            <>
                <Chart
                    chartType='LineChart'
                    width="100%"
                    height="200px"
                    data={this.props.weather.series}
                    options={options}
                />
                <WxGraphModal buttonTxt="Enlarge" series={this.props.weather.series} options={options} title={this.props.weather.label}/>
            </>
        );
    }
}
export default WxMainObsCardGraph;