import React from 'react';

import Table from 'react-bootstrap/Table';

export class WxSummaryCardRecords extends React.Component {

    render() {

        let headers = (
            Object.keys(this.props.weather).map((station, i) => (
                <th>{this.props.weather[station].station}</th>       
            ))
        )

        let table = this._createTable(this.props.weather);


        return(
            <Table responsive size="sm" className="bg-light text-black">
                <thead className="text-capitalize">
                    <th></th>{headers}
                </thead>
                <tbody>
                    {table}
                </tbody>
            </Table>
        )
    }

    _createTable(data) {

        return (
            <>
            <tr>
                <th>Max</th>{
                    Object.keys(this.props.weather).map((station, i) => (
                        <th>{parseFloat(this.props.weather[station].max).toString()}</th>       
                    ))
                }
            </tr>
            <tr>
                <th>Current</th>{
                    Object.keys(this.props.weather).map((station, i) => (
                        <th>{parseFloat(this.props.weather[station].current).toString()}</th>       
                    ))
                    
                }
            </tr>
            <tr>
                <th>Min</th>{
                    Object.keys(this.props.weather).map((station, i) => (
                        <th>{parseFloat(this.props.weather[station].min).toString()}</th>       
                    ))
                }
            </tr>
            </>
        )
    }
}
export default WxSummaryCardRecords