import {TileLayer, WMSTileLayer} from 'react-leaflet';
import React from 'react';

export class WxRadarEngine extends React.Component {

    radarImagesNew = [
        {
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m55m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m50m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m45m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m40m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m35m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m30m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m25m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m20m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m15m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m10m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        },{
            url: `https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913-m05m/{z}/{x}/{y}.png`,
            opacity: "0.0"
        }
    ]

    radarCurrent = 0;        

    constructor(props) {
        super(props);

        this.state = {radar: this.radarImagesNew}
    }

    componentDidMount() {

        if (this.props.animate) {
            this.timer = setInterval(() => {this._advanceRadar()}, 333);
        }

    }

    componentWillUnmount() {
        clearTimeout(this.timer);
    }

    _getRadar() {
        return this.radarImages[this.radarCurrent]
    }

    _advanceRadar() {

        const newState = this.state.radar;

        // Set current layer opacity to 0%
        newState[this.radarCurrent].opacity = 0.0;
        
        
        if (this.radarCurrent < this.state.radar.length - 1) {
            this.radarCurrent += 1;
        } else {
            this.radarCurrent = 0;
        }
        

        // Set new layer opacity to 67%
        newState[this.radarCurrent].opacity = 0.60;
        
        // Set new state
        this.setState({radar: newState})

    }

    render() {

        return (
            Object.keys(this.state.radar).map((layer, i) => (
                <TileLayer key={'radarTileLayer' + i} url={this.state.radar[layer].url} format={"image/png"} opacity={this.state.radar[layer].opacity}/>
            ))
        )
            /*
                <WMSTileLayer
                    url="https://mesonet.agron.iastate.edu/cgi-bin/wms/nexrad/n0q.cgi?"
                    tms={false}
                    format={"image/png"}
                    opacity={0.60}
                    transparent={true}
                    layers={`nexrad-n0q-900913`}
                />
            */
            /*
            *   <TileLayer
                    url={`https://mesonet.agron.iastate.edu/cache/tile.py/1.0.0/nexrad-n0q-900913${this.props.radarData}/{z}/{x}/{y}.png`}
                    opacity={0.67}
                />
            */
    }
}

export default WxRadarEngine;