import React, { useState, useEffect } from 'react';

import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';

import WxMapComponent from '../wxmapcomponent';

import WxDailyForecastRow from './wxmodalcomponents/wxdailyforecastrow';
import WxHourlyForecastRow from './wxmodalcomponents/wxhourlyforecastrow';
import { nwsLatLonFlip } from '../../util/wxcolorutils';

export function WxForecastModal(data) {
    const [show, setShow] = useState(false);
    const [forecast, setForecast] = useState({});
    const [loaded, setLoaded] = useState(false);

    let gridApi = data.CONFIG.nwsApi.path + 'points/' + data.CONFIG.stations[data.station].lat + ',' + data.CONFIG.stations[data.station].lon;
    let storyApi = data.CONFIG.nwsApi.path + 'offices/' + data.CONFIG.stations[data.station].nwsOffice.toUpperCase() + '/headlines';
    let forecastBaseApi = data.CONFIG.nwsApi.path  +'gridpoints/' + data.CONFIG.stations[data.station].nwsOffice.toUpperCase();

    useEffect(() => {_parseForecast({grid: gridApi, story: storyApi, fore: forecastBaseApi}).then((forecastParsed) => {setForecast(forecastParsed); setLoaded(true)})},[]);

    if (loaded) {

        let dailyItems = _parseDaily(forecast.fore.daily, data.CONFIG.forecast);
        let hourlyItems = _parseHourly(forecast.fore.hourly, data.CONFIG.forecast);

        return (
            <>
                <Button size="sm" onClick={() => setShow(true)} className="bg-light bg-opacity-75 text-black border-dark">
                    Forecast
                </Button>
                <Modal
                    show={show}
                    onHide={() => setShow(false)}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="foreModal">
                            Station Forecast
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div style={{height: "200px"}}>
                            <WxMapComponent
                                id="foreMap"
                                station={data.station}
                                radarVisible={false}
                                stationVisible={true}
                                warnVisible={true}
                                polygons={forecast.fore.geometry}
                                variant='success'
                                CONFIG={data.CONFIG}
                            />
                        </div>
                        <Tabs id="forecastTabs" defaultActiveKey="foreDaily">
                            { forecast.story !== null && 
                                <Tab eventKey="foreStory" title="Story">
                                    <h4>Synopsis:</h4>
                                    <p>{forecast.story}</p>
                                </Tab>
                            }
                            <Tab eventKey="foreDaily" title="Daily">
                                <Table size="sm">
                                    <tbody>
                                        {dailyItems}
                                    </tbody>
                                </Table>
                                { forecast.fore.daily !== undefined && <p>Updated: {new Date(forecast.fore.daily.updated).toLocaleString()}</p>}
                            </Tab>
                            <Tab eventKey="foreHourly" title="Hourly">
                                <Table size="sm">
                                    <tbody>
                                        {hourlyItems}
                                    </tbody>
                                </Table>
                                { forecast.fore.hourly !== undefined && <p>Updated: {new Date(forecast.fore.hourly.updated).toLocaleString()}</p>}
                            </Tab>
                        </Tabs>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button size="sm" onClick={() => setShow(false)}>Close</Button>
                    </Modal.Footer>
                </Modal>
            </>
        )
    }
};

function _parseDaily(forecast, CONFIG) {
    let rows = [];

    if (forecast === undefined || forecast.periods === undefined || forecast.periods === null) return null;

    for (var i = 0; i < CONFIG.dayPeriods; i++) {
        rows.push(React.createElement(WxDailyForecastRow, {key: 'day' + forecast.periods[i].number, forecast: forecast.periods[i]}));
    }

    return rows;
}

function _parseHourly(forecast, CONFIG) {
    let rows = [];

    if (forecast === undefined || forecast.periods === undefined || forecast.periods === null) return null;

    for (var i = 0; i < CONFIG.hourPeriods; i++) {
        rows.push(React.createElement(WxHourlyForecastRow, {key: 'hour' + forecast.periods[i].number, forecast: forecast.periods[i]}))
    }

    return rows;
}

async function _parseForecast(api) {

    // Weather Story Data
    const storyData = async () => {
        const response = await fetch(api.story, {mode:'cors'});
        const parsed = await response.json();

        return await parsed['@graph'][0].summary;
    }

    // Weather Forecast Data
    const gridData = async () => {
        const response = await fetch(api.grid, {mode:'cors'});
        const parsed = await response.json();

        const forecastDaily = async (loc) => {
            let dailyApi = api.fore + "/" + loc.x + ',' + loc.y + '/forecast';
            const dailyResponse = await fetch(dailyApi, {mode:'cors'});
            const dailyParsed = await dailyResponse.json();

            return await dailyParsed;
        }
        const forecastHourly = async (loc) => {
            let hourlyApi = api.fore + "/" + loc.x + ',' + loc.y + '/forecast/hourly';

            const hourlyResponse = await fetch(hourlyApi, {mode:'cors'});
            const hourlyParsed = await hourlyResponse.json();

            return await hourlyParsed;
        }

        let daily = await forecastDaily({x: parsed.properties.gridX, y: parsed.properties.gridY});
        let hourly = await forecastHourly({x: parsed.properties.gridX, y: parsed.properties.gridY});

        return {daily: await daily.properties, hourly: await hourly.properties, geometry: await [nwsLatLonFlip(daily.geometry.coordinates)]};
    }

    return {story: await storyData(), fore: await gridData()};
    
}
export default WxForecastModal;