import React from 'react';
import Form from 'react-bootstrap/Form';
import Chart from 'react-google-charts';

import {getIndexedFilenames} from '../../util/wxcolorutils.js';

export class WxAlertLogPane extends React.Component {
    constructor(props) {
        super(props);

        this.state = {files: null,
            contents: null};
    }

    componentDidMount() {
        this._getDirContents(this.props.CONFIG.alertLogPath);
    }

    async _getFileContents(file) {

        // Get File Contents
        const getContents = async (file) => {
            const response = await fetch(file, {mode:'cors'});
            const parsed = await response.json();
            
            return await parsed;
        }

        this.setState({contents: await getContents(this.props.CONFIG.alertLogPath + '/' + file)});
    }

    async _getDirContents(file) {

        this.setState({files: await getIndexedFilenames(file, 'log')});
    }

    _parseAlertLog(contents) {
        let data = [["","Event", "Type", "Headline", "Area", {type: "date", label:"Issued"}, {type: "date", label: "Start"}, {type: "date", label:"End"}]];

        for (const alert in contents) {
            let severeValue = 1;
            if (contents === null) {
                return null;
            }
            
            if (contents[alert].event.indexOf('Warning') !== -1) {
                severeValue = 7;}
            else if (contents[alert].event.indexOf('Watch') !== -1) {
                severeValue = 4;
            } else {
                severeValue = 1;
            }

            let row = [
                severeValue,
                contents[alert].event,
                contents[alert].type,
                contents[alert].headline,
                contents[alert].area,
                new Date(contents[alert].effective),
                new Date(contents[alert].onset),
                new Date(contents[alert].ends)
            ];
            data.push(row);
        }
        return data;
    }

    render() {

        const tableFormatter = [
            {
                type: "ColorFormat",
                column: 0,
                ranges: [
                    [0,2,"green", "green"],
                    [3,5,"yellow", "yellow"],
                    [6,8,"red", "red"]
                ]
            },{
                type: "DateFormat",
                column: 5,
                options: {
                    pattern: "MM/dd/yyyy HH:mm:ss"
                }
            },{
                type: "DateFormat",
                column: 6,
                options: {
                    pattern: "MM/dd/yyyy HH:mm:ss"
                }
            },{
                type: "DateFormat",
                column: 7,
                options: {
                    pattern: "MM/dd/yyyy HH:mm:ss"
                }
            }
        ]

        const tableOptions = {
            allowHtml: true,
            title: `Alert Log ${this.state.file}`,
            curveType: 'function',
            pageSize: 50,
            sortColumn: 5,
            sortAscending: true,
            legend: {position: "top"}
        };

        let log = this._parseAlertLog(this.state.contents);

        return (
            <>
                <Form.Group>
                    <Form.Label>Select Log</Form.Label>
                    <Form.Select onChange={(e) => this._getFileContents(e.target.value)}>
                        { (this.state.files !== null) &&
                            Object.keys(this.state.files).map((file, i) => (
                                <option key={i} value={this.state.files[file]}>{this.state.files[file]}</option>
                            ))
                        }
                    </Form.Select>
                </Form.Group>
                <Chart
                    chartType="Table"
                    width="100%"
                    height="300px"
                    data={log}
                    options={tableOptions}
                    formatters={tableFormatter}
                />
            </>
        )
    }
}
export default WxAlertLogPane;