//@ts-check

// React Imports
import React from "react";

// React-Bootstrap Imports
import ListGroup from "react-bootstrap/ListGroup";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import Popover from "react-bootstrap/Popover";

import WxAlertTimelinePopover from './wxalerttimelinepopover';
import WxAlertInfoOffcanvas from "./wxalertinfooffcanvas";

/**
 * Class to display a weather alert item
 */
class WxAlertItem extends React.Component {
    
    timelinePopover = (
        <Popover>
            <WxAlertTimelinePopover alert={this.props.alert.properties}/>
        </Popover>
    );

    render() {

        let active = this._isActive();
        let expired = this._isExpired();
        this.variant = this._getSeverity();

        let faActive = <span className="text-black" title="Inactive"><i className="fa fa-flag"/></span>;
        let faExpired = <span></span>;
        if (active) faActive = <span className="text-danger" title="Active"><i className="fa fa-flag"/></span>;
        if (expired) faExpired = <span className="text-danger" title="Active Expired"><i className="fa fa-exclamation"/></span>;

        this.itemButtonText = "text-black";

        if (this.variant === "extreme" || this.variant === "danger") {
            this.itemButtonText = "text-white";
        }

console.log(this.variant);

        return (
            <ListGroup.Item as="li" variant={this.variant} className="pt-0 pb-0 ps-0 pe-0 m-0">
                <Container className="p-0 m-0">
                    <Row>
                        <Col xs={1} md={1} xl={2}>
                            {faActive}
                        </Col>
                        <Col xs={1} md={1} xl={2}>
                            {faExpired}
                        </Col>
                        <Col className="overflow-hidden">
                            <span>
                                <i className={" me-2 " + this.props.alert.properties.icon}/>
                                {this.props.alert.properties.event}
                            </span>
                        </Col>
                        <Col xs={4} lg={3}>
                            <OverlayTrigger trigger="click" placement="auto" overlay={this.timelinePopover}>
                                <Button variant={this.variant} className="ps-1 pe-1 pt-0 pb-0 m-0 border-dark">
                                    <i className={"fa fa-clock " + this.itemButtonText} />
                                </Button>
                            </OverlayTrigger>
                            <WxAlertInfoOffcanvas key={this.props.alert.properties.id} alert={this.props.alert} station={this.props.station} CONFIG={this.props.CONFIG} btnText={this.itemButtonText}/>
                        </Col>
                    </Row>
                </Container>
            </ListGroup.Item>
        )
    }

    _isActive() {
        if (new Date(this.props.alert.properties.onset).getTime() <= new Date().getTime()) return true; else return false;
    }

    _isExpired() {
        if (new Date().getTime() + (this.props.CONFIG.alertExpDelay) > new Date(this.props.alert.properties.expires).getTime()) return true; else return false;
    }

    _getSeverity() {
        if (this.props.alert.properties.severeLevel === 0) {
            this.props.alert.properties.severeColor = 'success';
        } else if (this.props.alert.properties.severeLevel === 1) {
            this.props.alert.properties.severeColor = 'info';
        } else if (this.props.alert.properties.severeLevel === 2) {
            this.props.alert.properties.severeColor = 'warning';
        } else if (this.props.alert.properties.severeLevel === 3) {
            this.props.alert.properties.severeColor = 'danger';
        } else if (this.props.alert.properties.severeLevel === 4) {
            this.props.alert.properties.severeColor = 'extreme';;
        } else this.props.alert.properties.severeColor = 'success';;

        return this.props.alert.properties.severeColor;
    }
}
export default WxAlertItem;