// React and PropTypes Imports
import React from "react";
import PropTypes from 'prop-types';

// React-Bootstrap Import
import Container from "react-bootstrap/Container";

/**
 * Class to display the Footer Element
 * 
 * Displays the Footer portion on the main page
 * 
 *      Extends: React.Component
 *      Main Component: React-Bootstrap Container Component
 * 
 *      Properties:
 *          CONSTS - wxconstantlib.json file siteMeta contents
 */
class WxFooterContainer extends React.Component {
    /**
     * Renders the component to the screen
     * @returns React Component
     */
    render() {
        return (
            <Container id="WxFooter" fluid className="text-center">
            <p className="m-0"><small><i>Version: <a href="https://weather.russellv2.com/wx/version.txt" target="_blank" rel="noreferrer">{this.props.CONSTS.version}</a> - Release Date: {this.props.CONSTS.date} - Author: {this.props.CONSTS.author} - Contact: {this.props.CONSTS.contact}</i></small></p>
            </Container>
        )
    }
}
export default WxFooterContainer

// Prop Types Declaration
WxFooterContainer.propTypes = {
    CONSTS: PropTypes.exact({
        version: PropTypes.string.isRequired,
        date:  PropTypes.string.isRequired,
        author: PropTypes.string.isRequired,
        contact: PropTypes.string.isRequired
    })
}